      getFileId() {
            let _this = this;
            sessionStorage.clear();
            // _this.$axios.get('http://localhost:8080/static/json/getFileId.json', {
            this.$axios.post('/J_BEFP_010_0002', {
                "token": this.$store.state.toKen,
                "orderId": this.allUserData.orderId,
                "pageIndex": "4",
            })
            .then((res) => {
                if(res.data.rtnCode == '200') {
                   let allFileId = res.data.records;

                    allFileId.map((item, index)=>{
                        
                        if(item.fileName == 'Checklist_01-A001-1-1.jpg') {
                            _this.imgFrontId = item.fileId
                            sessionStorage.setItem("imgFrontId", _this.imgFrontId)
                        }
                        
                        if(item.fileName == 'Checklist_01-A001-2-1.jpg') {
                            _this.imgContraryId = item.fileId
                            sessionStorage.setItem("imgContraryId", _this.imgContraryId)
                        }

                        if(item.fileName == 'head-1.jpg') {
                            _this.imgHId = item.fileId
                            sessionStorage.setItem("imgHId", _this.imgHId)
                        }
                        
                        if(item.fileName == 'contract-1.jpg') {
                            _this.imgZId = item.fileId
                            sessionStorage.setItem("imgZId", _this.imgZId)
                        }

                        if(item.fileName == 'Checklist_03-C004-00.jpg' || item.fileName == 'Checklist_03-C004-01.jpg' || item.fileName == 'Checklist_03-C004-02.jpg' || item.fileName == 'Checklist_03-C004-03.jpg' ) {
                            let imgOPA = ['','']
                            imgOPA[0] = item.fileId
                            _this.imgG.push(imgOPA)
                            sessionStorage.setItem('imgG', JSON.stringify(_this.imgG))
                        }

                        if(item.fileName == 'Checklist_13-L001-00.jpg' || item.fileName == 'Checklist_13-L001-01.jpg' || item.fileName == 'Checklist_13-L001-02.jpg' || item.fileName == 'Checklist_13-L001-03.jpg') {
                            let imgOPAD = ['','']
                            imgOPAD[0] = item.fileId
                            _this.imgOther.push(imgOPAD)
                            sessionStorage.setItem('imgOther', JSON.stringify(_this.imgOther))
                        }

                   })

                   _this.firstPic();
                } else {
                    MessageBox('注意', res.data.rtnMsg); 
                }
            })
            .catch(function(err) {
            //    alert(122494)
            })
        },
        firstPic () {
            let _this = this;
            //发送请求获取身份证正面照片
            this.imgFrontId = sessionStorage.getItem('imgFrontId');
            if(this.imgFrontId) {
                // this.$axios.get('http://localhost:8080/static/json/download.json', {
                this.$axios.post('/J_BEFP_010_0005', {
                    "token": this.$store.state.toKen,
                    "orderId": this.allUserData.orderId,
                    "pageIndex": "4",
                    "D_testFlag": "2", //1是删除，2是下载
                    "D_fileId": this.imgFrontId
                })
                .then((res) => {
                    if(res.data.rtnCode == '200') {
                        //图片到了
                        _this.imgFront = res.data.fileInfo;
                    } else {
                        //图片没到
                        _this.imgFront = './static/img/picture/front.png'
                    }
                })
                .catch(function(err) {
                    //图片没到
                    _this.imgFront = './static/img/picture/front.png'
                    MessageBox('注意', '网络错误！');
                })
            }

            //发送请求获取身份证反面照片
            this.imgContraryId = sessionStorage.getItem('imgContraryId');
            if(this.imgContraryId) {
                // this.$axios.get('http://localhost:8080/static/json/download.json', {
                this.$axios.post('/J_BEFP_010_0005', {
                    "token": this.$store.state.toKen,
                    "orderId": this.allUserData.orderId,
                    "pageIndex": "4",
                    "D_testFlag": "2", //1是删除，2是下载
                    "D_fileId": this.imgContraryId
                })
                .then((res) => {
                    if(res.data.rtnCode == '200') {
                        //图片到了
                        _this.imgContrary = res.data.fileInfo;
                    } else {
                        //图片没到
                        _this.imgContrary = './static/img/picture/contrary.png'
                    }
                })
                .catch(function(err) {
                    //图片没到
                    _this.imgContrary = './static/img/picture/contrary.png'
                    MessageBox('注意', '网络错误！');
                })
            }

            //发送请求获取头像
            this.imgHId = sessionStorage.getItem('imgHId');
            if(this.imgHId) {
                // this.$axios.get('http://localhost:8080/static/json/download.json', {
                this.$axios.post('/J_BEFP_010_0005', {
                    "token": this.$store.state.toKen,
                    "orderId": this.allUserData.orderId,
                    "pageIndex": "4",
                    "D_testFlag": "2", //1是删除，2是下载
                    "D_fileId": this.imgHId
                })
                .then((res) => {
                    if(res.data.rtnCode == '200') {
                        _this.tmdH = true;
                        //图片到了
                        _this.imgH = res.data.fileInfo;
                    } else {
                        //图片没到
                        _this.imgH = './static/img/picture/camera.png'
                        _this.tmdH = false
                    }
                })
                .catch(function(err) {
                    //图片没到
                    _this.imgH = './static/img/picture/camera.png'
                    _this.tmdH = false
                    MessageBox('注意', '网络错误！');
                })
            }

            //发送请求获取纸质合约
            this.imgZId = sessionStorage.getItem('imgZId');
            if(this.imgZId) {
                // this.$axios.get('http://localhost:8080/static/json/download.json', {
                this.$axios.post('/J_BEFP_010_0005', {
                    "token": this.$store.state.toKen,
                    "orderId": this.allUserData.orderId,
                    "pageIndex": "4",
                    "D_testFlag": "2", //1是删除，2是下载
                    "D_fileId": this.imgZId
                })
                .then((res) => {
                    if(res.data.rtnCode == '200') {
                        //图片到了
                        _this.tmdZ = true;
                        _this.imgZ = res.data.fileInfo;
                    } else {
                        //图片没到
                        _this.imgZ = './static/img/picture/camera.png'
                        _this.tmdZ = false
                    }
                })
                .catch(function(err) {
                    //图片没到
                     _this.imgZ = './static/img/picture/camera.png'
                    _this.tmdZ = false
                    MessageBox('注意', '网络错误！');
                })
            }
            //工作证明文件下载
            if(sessionStorage.getItem('imgG')){
                _this.imgG = JSON.parse(sessionStorage.getItem('imgG'))
            }
            if(_this.imgG.length != '0') {
                
                _this.tmdG = true;
                
                _this.imgG.forEach((item,index) => {
                    // this.$axios.get('http://localhost:8080/static/json/download.json', {
                    _this.$axios.post('/J_BEFP_010_0005', {
                        "token": this.$store.state.toKen,
                        "orderId": this.allUserData.orderId,
                        "pageIndex": "4",
                        "D_testFlag": "2", //1是删除，2是下载
                        "D_fileId": item[0]
                    })
                    .then((res) => {
                        
                        if(res.data.rtnCode == '200') {
                            _this.imgG[index][1] = res.data.fileInfo;
                            sessionStorage.setItem('imgG', JSON.stringify(_this.imgG))
                        } else {
                            _this.imgG.splice(0, 4);
                            sessionStorage.setItem('imgG', JSON.stringify(_this.imgG))
                            MessageBox('注意', res.data.rtnMsg);
                            if(_this.imgG.length == '0') {
                                _this.tmdG = false;
                            }
                        }

                        if(_this.imgG.length == '0') {
                            _this.workPictureTitle = '请拍摄工作证明文件'
                        } else if (_this.imgG.length == '4') {
                            _this.workPictureTitle = '拍摄完成'
                        } else {
                            _this.workPictureTitle = '点击空白处继续拍摄，最多拍摄4张'
                        }

                        _this.showCloseG()//显示g6
                    })
                    .catch(function(err) {
                        _this.imgG.splice(0, 4);
                        sessionStorage.setItem('imgG', JSON.stringify(_this.imgG))
                        MessageBox('注意', '网络错误！');
                        if(_this.imgG.length == '0') {
                            _this.tmdG = false;
                        }

                        if(_this.imgG.length == '0') {
                            _this.workPictureTitle = '请拍摄工作证明文件'
                        } else if (_this.imgG.length == '4') {
                            _this.workPictureTitle = '拍摄完成'
                        } else {
                            _this.workPictureTitle = '点击空白处继续拍摄，最多拍摄4张'
                        }

                        _this.showCloseG()//显示g7
                    })
                })
            }   

            //other图片下载
            if(sessionStorage.getItem('imgOther')){
                _this.imgOther = JSON.parse(sessionStorage.getItem('imgOther'))
            }
            if(_this.imgOther.length != '0') {
                _this.tmdO = true;
                _this.imgOther.forEach((item,index) => {
                // _this.$axios.get('http://localhost:8080/static/json/download.json', {
                    _this.$axios.post('/J_BEFP_010_0005', {
                        "token": this.$store.state.toKen,
                        "orderId": this.allUserData.orderId,
                        "pageIndex": "4",
                        "D_testFlag": "2", //1是删除，2是下载
                        "D_fileId": item[0]
                    })
                    .then((res) => {
                        if(res.data.rtnCode == '200') {
                            let imgzj = JSON.parse(JSON.stringify(_this.imgOther))
                            imgzj[index][1] = res.data.fileInfo
                            // _this.imgOther[index][1] = res.data.fileInfo;
                            
                            sessionStorage.setItem('imgOther', JSON.stringify(imgzj))
                            getother();
                            
                        } else {
                            
                            _this.imgOther.splice(0, 4);
                            sessionStorage.setItem('imgOther', JSON.stringify(_this.imgOther))
                            MessageBox('注意', res.data.rtnMsg);
                            if(_this.imgOther.length == '0') {
                                _this.tmdO = false;
                            }
                        }

                        if(_this.imgOther.length == '0') {
                            _this.otherPictureTitle = '请拍摄其他文件'
                        } else if (_this.imgOther.length == '4') {
                            _this.otherPictureTitle = '拍摄完成'
                        } else {
                            _this.otherPictureTitle = '点击空白处继续拍摄，最多拍摄4张'
                        }
                
                        _this.showCloseO()//判断显示6

                    })
                    .catch(function(err) {
                        _this.imgOther.splice(0, 4);
                        sessionStorage.setItem('imgOther', JSON.stringify(_this.imgOther))
                        MessageBox('注意', '网络错误！');
                        if(_this.imgOther.length == '0') {
                            _this.tmdO = false;
                        }

                        if(_this.imgOther.length == '0') {
                            _this.otherPictureTitle = '请拍摄其他文件'
                        } else if (_this.imgOther.length == '4') {
                            _this.otherPictureTitle = '拍摄完成'
                        } else {
                            _this.otherPictureTitle = '点击空白处继续拍摄，最多拍摄4张'
                        }

                        _this.showCloseO()//判断显示7
                    })
                })
            }  
            
            function getother() {
                if(sessionStorage.getItem('imgOther')){
                    _this.imgOther = JSON.parse(sessionStorage.getItem('imgOther'))
                }
            }
        },

          showCloseG() {
            let _this = this;
            //判断显示
            if(_this.imgG.length == '0') {
                _this.gallCameraPic = ['./static/img/picture/camera.png', './static/img/picture/camera.png', './static/img/picture/camera.png', './static/img/picture/camera.png'];
                _this.ghide1 = false;
                _this.ghide2 = false;
                _this.ghide3 = false;
                _this.ghide4 = false;
            }
            if(_this.imgG.length == 1) {
                _this.gallCameraPic.splice(0, 1, _this.imgG[0][1]) 
                _this.gallCameraPic.splice(1, 1, './static/img/picture/camera.png') 
                _this.gallCameraPic.splice(2, 1, './static/img/picture/camera.png') 
                _this.gallCameraPic.splice(3, 1, './static/img/picture/camera.png') 
                _this.ghide1 = true;
                _this.ghide2 = false;
                _this.ghide3 = false;
                _this.ghide4 = false;
            }
            
            if(_this.imgG.length == 2) {
                _this.gallCameraPic.splice(0, 1, _this.imgG[0][1]) 
                _this.gallCameraPic.splice(1, 1, _this.imgG[1][1]) 
                _this.gallCameraPic.splice(2, 1, './static/img/picture/camera.png') 
                _this.gallCameraPic.splice(3, 1, './static/img/picture/camera.png') 
                _this.ghide1 = true;
                _this.ghide2 = true;
                _this.ghide3 = false;
                _this.ghide4 = false;
            }

            if(_this.imgG.length == 3) {
                _this.gallCameraPic.splice(0, 1, _this.imgG[0][1]) 
                _this.gallCameraPic.splice(1, 1, _this.imgG[1][1]) 
                _this.gallCameraPic.splice(2, 1, _this.imgG[2][1]) 
                _this.gallCameraPic.splice(3, 1, './static/img/picture/camera.png') 
                _this.ghide1 = true;
                _this.ghide2 = true;
                _this.ghide3 = true;
                _this.ghide4 = false;
            }

            if(_this.imgG.length == 4) {
                _this.gallCameraPic.splice(0, 1, _this.imgG[0][1]) 
                _this.gallCameraPic.splice(1, 1, _this.imgG[1][1]) 
                _this.gallCameraPic.splice(2, 1, _this.imgG[2][1]) 
                _this.gallCameraPic.splice(3, 1, _this.imgG[3][1]) 
                _this.ghide1 = true;
                _this.ghide2 = true;
                _this.ghide3 = true;
                _this.ghide4 = true;
            }
        },
        showCloseO() {
            let _this = this;
            //判断显示
            if(_this.imgOther.length == '0') {
                _this.allCameraPic = ['./static/img/picture/camera.png', './static/img/picture/camera.png', './static/img/picture/camera.png', './static/img/picture/camera.png'];
                _this.hide1 = false;
