                _this.hide4 = false;
            }
            if(_this.imgOther.length == 1) {
                _this.allCameraPic.splice(0, 1, _this.imgOther[0][1]) 
                _this.allCameraPic.splice(1, 1, './static/img/picture/camera.png') 
                _this.allCameraPic.splice(2, 1, './static/img/picture/camera.png') 
                _this.allCameraPic.splice(3, 1, './static/img/picture/camera.png') 
                
