    color-stop(50%, transparent)
  );
  background-image: linear-gradient(
    0deg,
    #d9d9d9,
    #d9d9d9 50%,
    transparent 50%
  );
  background-size: 100% 0px;
  background-repeat: no-repeat;
  background-position: bottom;
