import axios from 'axios'
import crypto from 'crypto'
export default {
  // _baseUrl: "http://182.181.105.64:9080/befp",
  // _baseUrl:"http://182.181.105.143:6080/fhdz/ct",
  _baseUrl:window.location.origin+"/fhdz/ct",
  // _baseUrl:'http://182.181.105.64:6080/fhdz/ct',
  _post: function (url, date) {

    let key = "751f621ea5c8f930";
    let iv = "1234569987654321";

    let fullUrl = this._baseUrl + url;
    console.log(fullUrl);
    let vm = this;

    let instance = axios.create({
      baseURL: vm._baseUrl,
      timeout: 31000
    });

    instance.interceptors.request.use(function (config) {
     
      if(config.data) {
        console.log(config.data)
        config.data = vm._aesEncrypt(key, iv, JSON.stringify(config.data));
      }
      return config

    }, function (err) {
      return Promise.reject(err);
    });

    instance.interceptors.response.use(function (res) {
      res.data = JSON.parse(vm._aesDecrypt(key, iv, res.data));

      if (res.data.rtnMsg === "未查到相关信息") {
        return Promise.reject(res.data.rtnMsg);
      }else if(res.data.rtnCode === "200"){
        return res;
      } else {
        console.log("request : " + res.data.rtnMsg);
        return Promise.reject(res.data.rtnMsg);
      }

    }, function (error) {
      console.log("intercpt : " + error.message);
      // vm.$router.replace('noauth');
      return Promise.reject(error.message);
    });


    return instance.post(fullUrl, date)

  },

  _get: function (url, data) {
    let fullUrl = this._baseUrl + url;

    return axios.get(fullUrl, {
      params: data
    })
  },

  _aesEncrypt: function (key, iv, data) {
    let cipher = crypto.createCipheriv('aes-128-cbc', key, iv);
    let crypted = cipher.update(data, 'utf8', 'binary');
    crypted += cipher.final('binary');
    crypted = new Buffer(crypted, 'binary').toString('base64');
    return crypted;
  },

  _aesDecrypt: function (key, iv, crypted) {
    crypted = new Buffer(crypted, 'base64').toString('binary');
    let decipher = crypto.createDecipheriv('aes-128-cbc', key, iv);
    let decoded = decipher.update(crypted, 'binary', 'utf8');
    decoded += decipher.final('utf8');
    return decoded;
  },  

  api: {
    /**
     * 行业类别
     */
    _industry: "/J_BEFP_000_0034",

    _work:"/J_BEFP_010_0011",

    _detail:"/J_BEFP_010_0001",

    _extra:"/J_BEFP_010_0001",

    _information:"/J_BEFP_010_0002",


    _preApprove:"/J_BEFP_010_0007",

    //验证码
    _sendAuth: "/J_BEFP_010_0006",
    _getAuth: "/J_BEFP_010_0006",

    //卡类卡产品
    _getCard: "/J_BEFP_000_0031",
    _sendCard: '/J_BEFP_010_0001',
    //选项查询
    _getCardSelected: "J_BEFP_010_0002",

    //邮件发送
    _sendMail: '/J_BEFP_010_0001',
    //邮寄地址查询
    _getMail: '/J_BEFP_010_0002'

  }
}



