<template>
  <div class="check-wrapper">
    <div class="check-top"></div>
    <div class="check-content">
      <ul>
        <li>
          <div 
            v-for="(item, index) in cardVariety" 
            :key="item.id" 
            :class="{active:index == num}"
            @click="itemClick(index)"
            class="item">
            {{ item }}
          </div>
        </li>
      </ul>
    </div>
    <div class="line"></div>
    <div class="card-wrapper">
      <div
      class="card-item"
      v-for="(item, index) in cardData" 
      :key="item.id">
        <img class="card-img" v-lazy="'./static/img/cardface/'+ item.D_cardTypeCode
 +'.jpg'" alt="">
        <h2>{{ item.D_cardProdName }}</h2>
        <!-- <p>{{ item.detail }}</p> -->
        <div id="selected-btn" class="selected-btn">
          <el-radio @change="changeCard(index+1)" v-model="radio" :label="index+1">请选择</el-radio>
        </div>
      </div>
    </div>
    <div class="selected-org">
      <span style="color:rgb(68, 120, 240)">发卡组织：</span>
      <el-checkbox v-model="checkedC" disabled>银联</el-checkbox>
      <el-checkbox v-model="checkedV" disabled>VISA</el-checkbox>
      <el-checkbox v-model="checkedM" disabled>MASTERCARD</el-checkbox>
    </div>
    <div class="eight">
      <el-checkbox v-model="agreeChecked" :disabled="checkedAgree"></el-checkbox>
      <span style="font-size: .28rem;" class="agree">同意在符合条件的情形下核发白金卡，点击查看<span @click="bjkxz()" style="color:#4478f0;">《白金卡细则》</span></span>
    </div>
    <div class="next-btn">
      <button v-on:click="infoNextcheck">
          下一步：填写个人信息
      </button>
    </div>
  </div>
</template>

<script>
  import { MessageBox, Indicator } from 'mint-ui';
  export default {
    name: "check",
    data() {
      return {
        num: 0,
        checkedC: true,
        checkedV: false,
        checkedVdis: true,
        checkedM: false,
        checkedMdis: true,
        agreeChecked: false,
        checkedAgree: true,
        agreeData: '', 
        radio:'',
        cardOrgCode: 'C',
        checkedkind: ['银联', 'VISA', 'MAS'],
        cardVariety: ['全部', '人气推荐', '优逸白金', '活力青年', '视频游戏', '商务旅行', '商超购物', '汽车系列'],
        card: [
            {
              "D_cardProdName": '银联优逸白金卡银联优逸白金卡银联优逸白金卡',
              detail: '首年免年费|刷6次免次年年费'
            },
            {
              "D_cardProdName": '银联优逸白金卡',
              detail: '首年免年费|刷6次免次年年费'
            },
            {
              "D_cardProdName": '银联优逸白金卡',
              detail: '首年免年费|刷6次免次年年费'
            },
            {
              "D_cardProdName": '银联优逸白金卡',
              detail: '首年免年费|刷6次免次年年费'
            },
            {
              "D_cardProdName": '银联优逸白金卡',
              detail: '首年免年费|刷6次免次年年费'
            },
            {
              "D_cardProdName": '银联优逸白金卡',
              detail: '首年免年费|刷6次免次年年费'
            }
        ],
        cardData: [

        ],
        allData: [

        ],
        allfilterData: [

        ],
        bzbjData: [

        ],
        yybjData: [

        ],
        hlqnData: [

        ],
        spyxData: [

        ],
        swlxData: [

        ],
        scgwData: [

        ],
        qcxlData: [

        ],
      }
    },
    computed: {
      allUserData () {
        return this.$store.state.allUserData
      },
      token(){
        return this.$store.state.toKen;
      },
      checkData() {
        return {
          "token": this.token,
          "pageIndex": '1',
          "orderId": this.allUserData.orderId,
          "cardOrgCode": this.cardOrgCode,
          "prodCd": this.cardData[this.radio - 1].D_cardProdCode,
          //和prodCd相同,要判断是否勾选
          "upProCD": this.upProCD(),
          //要判断是否勾选
          "upCardOrgCode": this.bdbq(),
          "flag": this.cardData[this.radio - 1].D_goldRegulerCode.trim(),
          "cardType": this.cardData[this.radio - 1].D_cardTypeCode,
          // "classUpFlag": this.agreeChecked == false ? '0' : '1',
          "classUpFlag": this.checkedAgreeData ()
        }
      },
    },
    created (){
      
    },
    mounted() {
      this.$nextTick(() => {
        this.getCardInfo();
      }) 
    },
    methods: {
      bjkxz() {
        MessageBox({
          title: '',
          message: `如您符合白金卡发卡标准，本行将直接为您核发同卡种白金卡，
                    且该白金卡无论是否激活，均将收取基本年费，如您勾选则代表同意。
                    --白金卡基本年费为1000元/年，无论您是否激活卡片都将在核卡后收取。
                    --白金卡年费在核卡后产生，年费周期为核卡当月起的连续12个月。
                    白金卡除具有标准信用卡的各项功能外，我们为您精心设计了基础增值服务。
                    您激活卡片后即可享有我行免费赠送全球机场贵宾室服务、酒后代驾服务、全面高额的家庭旅行保险、用卡无忧和信用保障保险、分行网点贵宾通道、白金卡专属积分计划、白金卡特惠商户、多项免费尊享服务和白金秘书服务等。`,
          confirmButtonText: '知道了'
        });
      },
      firstPicSelected () {
         let _this = this;
         this.$axios.post(this.$Rest.api._getCardSelected, {
          "token": this.$store.state.toKen,
          "orderId": this.allUserData.orderId,
          "pageIndex": "1",
         })
         .then((res) => {
          if(res.data.rtnCode === '200') {
            if(res.data.records){
              let cardType = res.data.records[0].cardType;
              let isUpFlag = res.data.records[0].classUpFlag;
              
              _this.cardData.map((item, index) => {
                if(item.D_cardTypeCode === cardType) {
                  _this.radio = index + 1;
                  this.changeCard(index + 1);
                    if(res.data.records[0].cardOrgCode === 'C,V'){
                          this.checkedV = true;
                    }
                    if(res.data.records[0].cardOrgCode === 'C,M'){
                      this.checkedM = true;
                    }
                    if(res.data.records[0].cardOrgCode === 'C'){
                      this.checkedM = false;
                      this.checkedV = false;
                    }
                }
              })

              if(isUpFlag === '0') {
                this.agreeChecked = true;
                this.checkedAgree = false
              }

              if(isUpFlag === '1') {
                this.agreeChecked = false;
                this.checkedAgree = false
              }

              if(isUpFlag === '2') {
                this.agreeChecked = false;
                this.checkedAgree = true 
              }
           } 
          } else {
            this.$store.commit('changeShow', 'error');
          }
           
         })
         .catch((err) => {
            console.log(err)
         })
         
      },
      itemClick (index) {
        this.num = index;
        this.radio = '';
        if(index === '0'){
          this.cardData = this.allfilterData
        }

        if(index === '1') {
          this.cardData = this.bzbjData
        }

        if(index === '2') {
          this.cardData = this.yybjData
        }

        if(index === '3') {
          this.cardData = this.hlqnData
        }

        if(index === '4') {
          this.cardData = this.spyxData
        }

        if(index === '5') {
          this.cardData = this.swlxData
        }

        if(index === '6') {
          this.cardData = this.scgwData
        }

        if(index === '7') {
          this.cardData = this.qcxlData
        }
      },
      upProCD() {
        if(this.agreeChecked === false && this.checkedAgree === true ) {
            return ''
          }

          if(this.agreeChecked === false && this.checkedAgree === false ) {
            return ''
          }

          if(this.agreeChecked === true && this.checkedAgree === false) {
           return this.cardData[this.radio - 1].D_cardProdCode
          }
      },
      bdbq() {
          if(this.agreeChecked === false && this.checkedAgree === true ) {
           
            return ''
          }

          if(this.agreeChecked === false && this.checkedAgree === false ) {
           
            return ''
          }

          if(this.agreeChecked === true && this.checkedAgree === false) {
            if(!this.checkedV && !this.checkedM) {
              return 'C'
            }
            if(this.checkedV) {
              return 'V'
            }
            if(this.checkedM) {
              return 'M'
            }
          }
      },
      checkedAgreeData () {
          if(this.agreeChecked === false && this.checkedAgree === true ) {
            return '2'
          }

          if(this.agreeChecked === false && this.checkedAgree === false ) {
            return '1'
          }

          if(this.agreeChecked === true && this.checkedAgree === false) {
            return '0'
          }
      },
      infoNextcheck () {
        let _this = this;
        if(!this.radio) {
          MessageBox('注意', '请先选择卡产品！');
          return
        }

        if(this.checkedV) {
          this.cardOrgCode = 'C,V'
        }
        if(this.checkedM) {
          this.cardOrgCode = 'C,M'
        }

        Indicator.open();
        this.checkedAgreeData();
        console.log(this.checkData);
        this.$axios.post(this.$Rest.api._sendCard, this.checkData)
        .then((res) => {
            console.log(res)
            if(res.data.rtnCode === '200') {
              Indicator.close();
              this.$router.push('/detail')
            } else {
              Indicator.close();
              MessageBox('注意', res.data.rtnMsg);
            }
        }).catch(function(err) {
            Indicator.close();
            MessageBox('注意', '网络错误！');
        })
      },
      changeCard (index) {
        console.log(this.radio)
        let isClassUp = this.cardData[index-1].D_literLevel.split(',');
        //是否可升等
        console.log(isClassUp)
       
        if(this.cardData[index-1].D_cardIssueCode.trim() === 'C') {
          this.checkedVdis = true
          this.checkedMdis = true
          this.checkedV = false
          this.checkedM = false
          
          this.agreeChecked = false;
          this.checkedAgree = true;
          if(isClassUp[0] === 'Y' && isClassUp[1] === '1') {
            this.agreeChecked = true;
            this.checkedAgree = false;
          }
        }

        if(this.cardData[index-1].D_cardIssueCode.trim() === 'V') {
          this.checkedMdis = true
          this.checkedVdis = false
          this.checkedV = true;

          this.agreeChecked = false;
          this.checkedAgree = true;
          if(isClassUp[0] === 'Y' && isClassUp[2] === '1') {
            this.agreeChecked = true;
            this.checkedAgree = false;
          }
        }

        if(this.cardData[index-1].D_cardIssueCode.trim() === 'M') {
          this.checkedVdis = true
          this.checkedMdis = false
          this.checkedM = true;

          this.agreeChecked = false;
          this.checkedAgree = true;
          if(isClassUp[0] === 'Y' && isClassUp[3] === '1') {
            this.agreeChecked = true;
            this.checkedAgree = false;
          }
        }
        
      },
      //获取卡名称，卡活动, 卡类型以及卡其他信息
      //获取卡图片链接
      getCardInfo() {
        Indicator.open()
        this.$axios.post(this.$Rest.api._getCard, {
          "toKen": this.token,
          "D_citysId": this.allUserData.citycode
        })
        .then((res) => {
            this.allData = res.data.message;

            let tData = [];
            this.allData.map((item, index) => {
              if(item.D_cardTypeCode !== "966" && item.D_cardTypeCode !== "882" && item.D_cardProdCode !== "089" && item.D_cardProdCode !== "091" && item.D_cardProdCode !== '110'  && item.D_cardProdCode !== '111' && item.D_cardProdCode !== "113" ) {
                tData.push(item);
              }
            });

            tData.map((item, index) => {
              if(item.D_cardProdCode == '106' || item.D_cardProdCode == '003' || item.D_cardProdCode == '099' || item.D_cardProdCode == '083') {
                 //现在是人气推荐
                this.bzbjData.push(item)
              }
              if(item.D_cardProdCode == '006' && item.D_goldRegulerCode.trim() == 'G') {
                //现在是人气推荐
                this.bzbjData.push(item)
              }
              if(item.D_cardProdCode == '001' && item.D_goldRegulerCode.trim() == 'G') {
                  //现在是人气推荐
                this.bzbjData.push(item)
              }
              if(item.D_cardProdCode == '106') {
                this.yybjData.push(item)
              }

              if(item.D_cardProdCode == '004' || item.D_cardProdCode == '003' || item.D_cardProdCode == '095' || item.D_cardProdCode == '091' || item.D_cardProdCode == '089' ) {
                this.hlqnData.push(item)
              }

              if(item.D_cardProdCode == '016' || item.D_cardProdCode == '088') {
                this.qcxlData.push(item)
              }
              
              if(item.D_cardProdCode == '123' || item.D_cardProdCode == '111' || item.D_cardProdCode == '110' || item.D_cardProdCode == '083' || item.D_cardProdCode == '085' || item.D_cardProdCode == '113') {
                this.spyxData.push(item)
              }

              if(item.D_cardProdCode == '078' || item.D_cardProdCode == '018' || item.D_cardProdCode == '039' || item.D_cardProdCode == '099') {
                this.swlxData.push(item)
              }

              if( item.D_cardProdCode == '055' || item.D_cardProdCode == '048' || item.D_cardProdCode == '097' || item.D_cardProdCode == '064' || item.D_cardProdCode == '006' || item.D_cardProdCode == '117' || item.D_cardProdCode == '067' || item.D_cardProdCode == '007' || item.D_cardProdCode == '017' || item.D_cardProdCode == '065' || item.D_cardProdCode == '066' || item.D_cardProdCode == '074' || item.D_cardProdCode == '075') {
                this.scgwData.push(item)
              }
            });
            this.allfilterData = tData;
            this.cardData = this.allfilterData;
            Indicator.close();
            this.firstPicSelected();
            
        })
        .catch(function(err) {
            Indicator.close();
            MessageBox('注意', '网络错误！');
        })
      }
    }
  }
</script>

<style scoped lang="scss"> 
  .check-wrapper {
    width: 100%;
    margin: 0;
    padding: 0;
    .check-top {
      padding-top: 7px; 
      background: #eeeeee;
    }
    .check-content {
      padding-left: 13px;
      padding-right: 13px;
      background: #fff;
      &>ul>li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: .3rem;
        flex-wrap: wrap;
      }
      .item {
        margin-top: .28rem;
        border: 1px solid #b2b2b2;
        width: 1.6rem;
        height: .7rem;
        border-radius: 35px;
        text-align: center;
        line-height: .7rem;
        flex-grow: 0;
        flex-shrink: 0;
      }
      .active {
        border: 1px solid #2879fe;
        color: #3374f6;
        font-weight: 700;
      }
    }
    .line {
      padding-top: 15px;
      border-bottom: 1px solid #e8e8e8;
      width: 86%;
      margin: 0 auto;
    }
    .card-wrapper {
      padding-top: .08rem;
      width: 5.84rem;
      height: 5.4rem;
      overflow: auto;
      
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      .card-item {
        width: 2.76rem;
        padding-bottom: .2rem;
        text-align: center;
        background: #fff;
        .card-img {
          display: block;
          margin: 0 auto;
          width: 2.3rem;
          height: 1.46rem;
        }
        h2 {
          padding-top: .2rem;
          padding-bottom: .1rem;
          font-size: .28rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          color: #000;
        }
        p {
          padding-top: .12rem;
          padding-bottom: .34rem;
          font-size: .20rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .selected-btn {
          width: 2.22rem;
          height: .52rem;
          background: #2879ff;
          color: #fff;
          line-height: .52rem;
          border-radius: .2rem;
          font-size: .3rem;
          margin: 0 auto;
          .el-radio {
            color: #fff;
          }
        }
      }
    }
    .selected-org {
        padding-top: .1rem;
        color: #343434;
        padding-left: .50rem;
        .el-checkbox {
          margin-left: .2rem;
        }
    }
    .eight {
      display: flex;
      align-items: flex-start;
      padding-left: .5rem;
      padding-top: .2rem;
    }
    .agree {
      padding-left: .1rem;
    }
    .next-btn {
        text-align: center;
        margin-top: .5rem;
        padding-bottom: .2rem;
        button {
            width: 5rem;
            height: .8rem;
            border: 2px solid #fff;
            color: #fff;
            background: #3174f6;
            margin: 0 auto;
            border-radius: 40px;
            outline: none;
        }
    } 
  }
</style>



