    hz2py:function  (str) {
        let vm = this;
        var l2 = str.length;
        var ret = "";
        var reg = new RegExp('[a-zA-Z0-9\- ]');
        for (var i = 0; i < l2; i++) {
            var val = str.substr(i, 1);
            if (reg.test(val)) {
                ret += val;
            } else {
                var py = vm.arraySearch(val);
                if (py !== false) {
                    ret += " " + py;
                }
            }
        }
        while (ret.indexOf("  ") > 0) {
            ret = ret.replace("  ", " ");
        }
        if (ret.charAt(0) == " ") {
            ret = ret.substr(1, ret.length - 1);
        }
        return ret;
    },
    
    arraySearch:function (c) {
        let vm = this;
        for (var name in vm.PinYin) {
            if (vm.PinYin[name].indexOf(c) != -1) {
                return vm.ucAll(name); break;
            }
        }
        return false;
    },
    
    ucAll:function (str) {
        return str.toUpperCase();
    },
    
    ucfirst:function (str) {
        if (str.length > 0) {
            var first = str.substr(0, 1).toUpperCase();
            var spare = str.substr(1, str.length);
            return first + spare;
        }
    }

