import {Indicator, Toast} from 'mint-ui';

export default {
  showLoading: function (message) {

    let text = "请稍后...";
    if (message !== null && message !== "") {
      text = message
    }
    Indicator.open({
      text: text,
      spinnerType: 'triple-bounce'
    });
  },

  hideLoading: function () {
    Indicator.close();
  },

  showToast(message) {
    Toast({
      message: message,
      position: 'center',
      duration: 2000
    });
  }
}



