// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import devices from './base/BaseDevice'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import mui from 'mint-ui';
import 'mint-ui/lib/style.css';
import tools from './base/BaseTools'
import rest from './base/BaseRest';
import store from './store/index';
import VueAreaLinkage from 'vue-area-linkage';
import 'vue-area-linkage/dist/index.css';
import jiami from './base/jiami';
import axios from 'axios';
import router from './router'

Vue.use(VueAreaLinkage);

Vue.use(mui);
Vue.use(ElementUI);


let axiosUrl = window.location.protocol + '//' + window.location.host + '/fhdz/ct';

// axios.defaults.baseURL = axiosUrl;
axios.defaults.baseURL = 'http://182.180.198.43:8080/fhdz/ct';


Vue.prototype.$axios = axios;
axios.defaults.withCredentials = false;
axios.defaults.timeout = 10000;
axios.defaults.headers['Content-Type'] = 'application/json';

axios.interceptors.request.use(
  config => {

    if (config.data) {
      config.data = jiami.enJiaMi(config.data);
    }
    return config
  },
  err => {
    return Promise.reject(err);
  }
);

axios.interceptors.response.use(
  res => {
    res.data = jiami.deJiaMi(res.data);
    return res
  },
  err => {
    return Promise.reject(err);
  }
);


Vue.config.productionTip = false;
Vue.prototype.$devices = devices;
Vue.prototype.$tools = tools;
Vue.prototype.$Rest = rest;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: {App},
  template: '<App/>'
});






