<template>
    <div class="complete-wrapper">
        <div class="complete-top"></div>
        <h3 style="text-align: left;padding-left: 40px;">亲爱的客户:</h3>
        <div class="text">
            <p style="padding-bottom: 10px;">恭喜您完成了基本信息的提交。</p>
            <p style="padding-bottom: 10px;">请将您刚刚填写的纸质协议书交给您的分行客户经理，并告知客户经理完成您申请件的审批和提交。</p>
            <p>感谢您选择交通银行信用卡!</p>
        </div>
        <!-- <div class="next-btn">
            <button v-on:click="recommend">
                立即推荐办卡
            </button>
        </div>
        <div class="next-btn download">
            <button v-on:click="recommend">
                下载买单吧APP
            </button>
        </div> -->
        <div class="ewm">
            <!-- <img src="../../assets/complete/ewm.jpg" alt=""> -->
        </div>
    </div>
</template>

<script>
export default {
    name: 'complete',
    data () {
        return {

        }
    },
    methods: {
        recommend () {
            console.log('推荐办卡!')
        }
    }
}
</script>

<style scoped lang="scss">
.complete-wrapper {
    .complete-top {
        width: 7.5rem;
        height: 4.7rem;
        background: url("../../assets/complete/completetop.jpg") no-repeat;
        background-size: cover;
    }
    > h3 {
        font-size: .3rem;
        text-align: center;
        color: #626262;
        font-weight: 700;
    }
    .text {
        padding-top: .3rem;
        padding-left: .8rem;
        padding-right: .8rem;
        p {
             line-height: .5rem;
             color: #9c9c9c;
        }
        p:first-child {
            color: #ff8385;
        }
    }
    .next-btn {
        text-align: center;
        margin-top: .7rem;
        padding-bottom: .2rem;
        button {
            width: 5rem;
            height: .8rem;
            border: 2px solid #fff;
            color: #fff;
            background: #3174f6;
            margin: 0 auto;
            border-radius: 40px;
            outline: none;
        }
    }
    .download {
        margin-top: .4rem;
        button {
            background: none;
            border: 1px solid #cccccc;
            color: #2a77ff;
        }
    }
    .ewm {
        padding-top: .4rem;
        img {
            display: block;
            width: 1.54rem;
            height: 1.56rem;
            margin: 0 auto;
        }
    }
}
</style>



