<template>
  <div :class="[{'hide-all': isHide}, 'credit-style']" >
        <div class="close" @click="hideRevenue()">X</div>
        <p>
            交通银行将按照《非居民金融账户涉税信息尽职调查管理办法》要求，向国家税务主管机关报送相关客户涉税信息，国家税务主管机关将根据国际多边协议向其他税收管辖区的税务当局转交相关客户涉税信息。
        </p>
        <P>
            账户持有人在相关信息发生变更时，应在30日内通知交通银行，否则将由账户持有人承担由此造成的不利后果
        </P>
        <P>
            中国税收居民个人是指在中国境内有住所，或者无住所而在境内居住满一年的个人。在中国境内有住所是指因户籍、家庭、经济利益关系而在中国境内习惯性居住。在境内居住满一年，是指在一个纳税年度中在中国境内居住365日。临时离境的，不扣减日数。临时离境，是指在一个纳税年度中一次不超过30日或者多次累计不超过90日的离境。
        </P>
        <P>
            非居民是指中国税收居民以外的个人。其他国家（地区）税收居民身份认定规则及纳税人识别号相关信息请参见国家税务总局网站。
        </P>
        <P>
            此处“美国个人”是指美国国籍、美国绿卡持有者或美国长期逗留的外国人。美国长期逗留的外国人指：当年内待满31天以上，并在三年内累计超过183天，累计方式是当年每一天算一天，去年每一天算三分之一天，前年每一天算六分之一天。
        </P>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
      return {

      }
  },
  computed: {
    isHide() {
        return this.$store.state.showRevenue
    }
  },
  methods: {
        hideRevenue () {
            this.$store.commit('SHOW_REVENUE', true)
        }
  }
}
</script>

<style scoped lang="scss">
    .hide-all {
        display: none;
    }
    .credit-style {
        width: 7.5rem;
        height: 13.34rem;
        padding: .3rem;
        background: #fff;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 998;
        .close {
            font-size: .4rem;
        }
        h2 {
           font-size: .4rem;
           text-align: center;
           line-height: .8rem;
           font-weight: 700;
        }
        p {
            text-indent: .6rem;
            font-size: .3rem;
            line-height: .5rem;
        }
    }
</style>




