<template>
  <div class="mail-address">
    <div class="check-top"></div>
    <div class="home-wrapper">
      <el-form id="mail-form" label-width="" ref="homeForm" :model="familyAddressInfo">
        <div class="home-address">家庭地址</div>
        <div>
          <area-select type="text" :placeholders=selectedHint class="area-select" :level="2"
                       v-model="familyAddressInfo.alladdress" :data="pcaa"></area-select>
        </div>

        <mt-field @blur.native.capture="addressTF()" class="commont" label="详细地址：" placeholder="请输入具体地址"
                  v-model="familyAddressInfo.address"></mt-field>

        <mt-field type="number" @blur.native.capture="postTF()" class="commont" label="邮政编码：" placeholder="请输入您的邮政编码"
                  v-model="familyAddressInfo.postCode"></mt-field>

        <!-- <div class="commont homePhoneClass">
          <div class="min-wrapper">
            <span class="title">家庭电话：</span><input @blur="precodeTF()" v-model="familyAddressInfo.preCode" class="zone-num" type="number" placeholder="区号">——<input @blur="phoneTF"  v-model="familyAddressInfo.familyPhone" class="phone-num-class" type="number" placeholder="电话号码">
          </div>
        </div> -->

        <mt-field :attr="{maxLength:11}" @blur.native.capture="relativephoneTF()" id="phone-style" class="commont"
                  label="直系亲属手机号：" placeholder="请输入您的直系亲属手机号" v-model="familyAddressInfo.relationPhone"></mt-field>


        <el-row class="commont family-relation">
          <el-col class="family-relation-title" :span="14">
            电话与所属人的关系：
          </el-col>
          <el-col class="family-relation-selected" :span="10">
            <el-select @change="familyRelationTF()" v-model="familyAddressInfo.familyRelation" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-col>
        </el-row>
      </el-form>
    </div>

    <div class="check-top"></div>

    <div class="company-wrapper">
      <el-form id="mail-form" label-width="" ref="companyForm" :model="corpAddressInfo">
        <div class="home-address">公司地址</div>
        <div>
          <area-select type="text" :placeholders=selectedHint class="area-select" :level="2"
                       v-model="corpAddressInfo.alladdress" :data="pcaa"></area-select>
        </div>
        <mt-field @blur.native.capture="addressTFC()" class="commont" label="详细地址：" placeholder="请输入具体地址"
                  v-model="corpAddressInfo.address"></mt-field>

        <mt-field @blur.native.capture="postTFC()" class="commont" label="邮政编码：" placeholder="请输入您的邮政编码"
                  v-model="corpAddressInfo.postCode"></mt-field>

        <div class="commont homePhoneClass">
          <div class="min-wrapper">
            <span class="title">公司电话：</span><input @blur="precodeTFC()" v-model="corpAddressInfo.preCode"
                                                   class="zone-num" type="number" placeholder="区号">—<input
            @blur="phoneTFC()" v-model="corpAddressInfo.corpPhone" class="phone-num-class" type="number"
            placeholder="电话号码">
            <span class="hengstyle">—</span><input @blur="_testbranchPhone()" max="6"
                                                   v-model="corpAddressInfo.branchPhone" class="branch-num-class"
                                                   type="number" placeholder="分机号"
                                                   oninput="if(value.length>6)value=value.slice(0,6)"></input>
          </div>
        </div>

      </el-form>
    </div>
    <div>
      <div id="postStyle" class="commont homeEmailClass">
        <div class="min-wrapper">
          <span id="postTitle" class="title">请选择邮寄方式：</span>
          <div class="post-select-wrapper">
            <el-radio class="post-home" v-model="postType.postType" label='0'>邮寄到家</el-radio>
            <el-radio v-model="postType.postType" label='1'>邮寄到公司</el-radio>
          </div>
        </div>
      </div>

    </div>
    <div class="next-btn">
      <button v-on:click="infoNextMail">
        下一步：增值服务
      </button>
    </div>
  </div>
</template>


<script>
  import {pca, pcaa} from 'area-data';
  import {Toast, MessageBox, Indicator} from 'mint-ui';

  let validPhone = (rule, value, callback) => {
    if (!value) {
      callback(new Error('请输入电话号码'))
    } else if (!isvalidPhone(value)) {
      callback(new Error('请输入正确的11位手机号码'))
    } else {
      callback()
    }
  };

  function isvalidPhone(str) {
    const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
    return reg.test(str)
  }

  export default {
    name: "mail",
    data() {
      return {
        detailRelationPhone: '',
        selectedHint: ['请选择省', '请选择市', '区/县'],
        pca: pca,
        pcaa: pcaa,
        options: [{
          value: '0',
          label: '配偶'
        }, {
          value: '1',
          label: '父母'
        }, {
          value: '2',
          label: '子女'
        }, {
          value: 'A',
          label: '其他直系亲属'
        }
        ],

        postOptions: [{
          value: '1',
          label: '邮寄到家'
        }, {
          value: '2',
          label: '邮寄到公司'
        }],

        postType: {
          postType: ''
        },
        familyAddressInfo: {
          alladdress: [],
          address: '',
          postCode: '',
          preCode: '',
          familyPhone: '',
          relationPhone: '',
          familyRelation: ''
        },
        corpAddressInfo: {
          alladdress: [],
          address: '',
          postCode: '',
          preCode: '',
          corpPhone: '',
          branchPhone: ''
        },
      }
    },
    computed: {
      allUserData() {
        return this.$store.state.allUserData
      },
      token() {
        return this.$store.state.toKen;
      },
      allData() {
        return {
          "token": this.$store.state.toKen,
          "pageIndex": "2",
          "orderId": this.allUserData.orderId,
          "custNo": "",
          "type": this.postType.postType,
          "provinceH": this.familyAddressInfo.alladdress[0],
          "cityH": this.familyAddressInfo.alladdress[1] === '市辖区' ? this.familyAddressInfo.alladdress[0] : this.familyAddressInfo.alladdress[1],
          "areaH": this.familyAddressInfo.alladdress[2],
          "addressH": this.familyAddressInfo.address,
          "postCodeH": this.familyAddressInfo.postCode,
          "preCodeH": this.familyAddressInfo.preCode,
          "phoneNoH": this.familyAddressInfo.familyPhone, //家庭电话
          "relationPhone": this.familyAddressInfo.relationPhone, //直系亲属手机号
          "familyRelation": this.familyAddressInfo.familyRelation,
          "province": this.corpAddressInfo.alladdress[0],
          "city": this.corpAddressInfo.alladdress[1] === '市辖区' ? this.corpAddressInfo.alladdress[0] : this.corpAddressInfo.alladdress[1],
          "area": this.corpAddressInfo.alladdress[2],
          "address": this.corpAddressInfo.address,
          "postCode": this.corpAddressInfo.postCode,
          "preCode": this.corpAddressInfo.preCode,
          "corpPhone": this.corpAddressInfo.corpPhone,
          "branchPhone": this.corpAddressInfo.branchPhone,
        }

      },
      firstData() {
        return {
          "token": this.$store.state.toKen,
          "orderId": this.allUserData.orderId,
          "pageIndex": "2",
        }
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.firstGetData();
      })
    },
    methods: {
      //传递数据，不能传市辖区
      firstGetData() {
        Indicator.open();
        this.$axios.post(this.$Rest.api._getMail, this.firstData)
          .then((res) => {
            Indicator.close();
            if (res.data.rtnCode === '200') {
              if (res.data.records) {

                let allMailData = res.data.records[res.data.records.length - 1];

                if (allMailData.cityH === '北京市' || allMailData.cityH === '上海市' || allMailData.cityH === '重庆市' || allMailData.cityH == '天津市') {
                  allMailData.cityH = '市辖区'
                }
                this.familyAddressInfo.alladdress = [allMailData.provinceH, allMailData.cityH, allMailData.areaH];
                this.familyAddressInfo.address = allMailData.addressH;
                this.familyAddressInfo.postCode = allMailData.postCodeH;
                this.familyAddressInfo.preCode = allMailData.preCodeH;
                this.familyAddressInfo.familyPhone = allMailData.phoneNoH;
                this.familyAddressInfo.relationPhone = allMailData.relationPhone;
                this.familyAddressInfo.familyRelation = allMailData.familyRelation;

                if (allMailData.city === '北京市' || allMailData.city === '上海市' || allMailData.city === '重庆市' || allMailData.city == '天津市') {
                  allMailData.city = '市辖区'
                }
                this.corpAddressInfo.alladdress = [allMailData.province, allMailData.city, allMailData.area];
                this.corpAddressInfo.address = allMailData.address;
                this.corpAddressInfo.postCode = allMailData.postCode;
                this.corpAddressInfo.preCode = allMailData.preCode;
                this.corpAddressInfo.corpPhone = allMailData.corpPhone;
                this.corpAddressInfo.branchPhone = allMailData.branchPhone;
                this.postType.postType = allMailData.type
              }
            } else {
              this.$store.commit('changeShow', 'error');
            }
          })
          .catch(function (err) {
            Indicator.close();
          });

        let _this = this;
        this.$axios.post(this.$Rest.api._information, {
          pageIndex: "3",
          orderId: this.$store.state.allUserData.orderId,
        })
          .then(function (res) {
            if (res.data.rtnCode === '200') {
              _this.detailRelationPhone = res.data.records[0].relationPhone
            }
          }).catch(function (err) {
        })
      },
      infoNextMail() {

        if (this.familyAddressInfo.alladdress.length !== 3) {
          MessageBox('', '请先选择家庭所在省市！');
          return
        }

        if (!this.addressTF()) {
          return
        }
        if (!this.postTF()) {
          return
        }
        if (!this.relativephoneTF()) {
          return
        }
        if (!this.familyRelationTF()) {
          return
        }

        if (this.corpAddressInfo.alladdress.length !== 3) {
          MessageBox('', '请选择公司所在省市！')
          return
        }

        if (!this._testbranchPhone()) {
          return
        }

        if (!this.addressTFC()) {
          return
        }
        if (!this.postTFC()) {
          return
        }
        if (!this.precodeTFC()) {
          return
        }
        if (!this.phoneTFC()) {
          return
        }

        if (!this.postType.postType) {
          MessageBox('', '请先选择邮寄方式')
          return
        }
        //家庭地址或者公司地址要包含一个城市
        let city = this.allUserData.cityname;
        // let isInclude = null;
        if (city === '上海' || city === "北京" || city === "天津" || city === "重庆") {
          let reg = new RegExp(city);
          //家庭
          let regCityH = this.familyAddressInfo.alladdress[0];
          let isIncludeH = reg.test(regCityH);

          //公司
          let regCity = this.corpAddressInfo.alladdress[0];
          let isInclude = reg.test(regCity)

        } else {
          let reg = new RegExp(city);
          //家庭
          let regCityH = this.familyAddressInfo.alladdress[1];
          let isIncludeH = reg.test(regCityH);

          //公司
          let regCity = this.corpAddressInfo.alladdress[1];
          let isInclude = reg.test(regCity)
        }

        if (!isIncludeH && !isInclude) {

          MessageBox('注意', '家庭地址和邮寄地址的所在城市中，至少有一个和客户经理所在城市保持一致')
          return
        }

        Indicator.open();
        console.log(this.allData);
        this.$axios.post(this.$Rest.api._sendMail, this.allData)
          .then((res) => {
            if (res.data.rtnCode === '200') {
              Indicator.close();
              this.$router.push('/extra')
            } else {
              Indicator.close();
              MessageBox('注意', res.data.rtnMsg);
            }
          })
          .catch(function (err) {
            Indicator.close();
            MessageBox('注意', '网络错误！');
          })

      },

      _test0E0F: function (str) {
        let temp = "";
        let reChn = /^[\u4e00-\u9fa5]/;
        let reChnTwo = /^[\u4e00-\u9fa5]{2}/;
        let reChnThird = /[\u4e00-\u9fa5]+/;
        let reChnFourth = /[\u4e00-\u9fa5]*/;
        let length = this._getLengthOfString(str);
        let list = [];
        if (str.length === 1) {
          if (reChn.test(str)) {
            length += 2;
          }
        }
        for (let i = 0; i < str.length - 1; i++) {
          let s = str.substring(i, i + 2);
          if (reChnTwo.test(s) && reChnFourth.test(temp)) {
            temp += str.substring(i + 1, i + 2);
            if (i === str.length - 2) {
              list.push(temp);
              temp = "";
              length += 2;
            }
          } else {
            //list.add(temp);
            if (reChnThird.test(str.substring(i, i + 1))) {
              length += 2;
            }
            temp = str.substring(i + 1, i + 2);
          }
        }
        return length;

      },

      _getLengthOfString: function (str) {
        let length = 0;
        for (let i = 0; i < str.length; i++) {
          let temp = str.substring(i, i + 1);
          if (this.lengthInUtf8Bytes(temp) === 3) {
            length += 2;
          } else if (this.lengthInUtf8Bytes(temp) === 1) {
            length += 1;
          }
        }
        return length;
      },

      lengthInUtf8Bytes: function (str) {

        let m = encodeURIComponent(str).match(/%[89ABab]/g);
        return str.length + (m ? m.length : 0);

      },

      // 家庭地址校验
      addressTF() {
        if (this.familyAddressInfo.address.length === 0) {
          Toast({
            message: '请输入家庭具体地址',
            duration: 1000
          });
          return false
        } else if (this.familyAddressInfo.address.length !== 0 && this.familyAddressInfo.address.length < 4) {
          Toast({
            message: '地址输入内容过少',
            duration: 1000
          });
          return false
        } else if (this._test0E0F(this.familyAddressInfo.address) > 55) {
          Toast({
            message: '家庭详细地址不能超过55位',
            duration: 1000
          });
          return false
        } else if (this.familyAddressInfo.address.length > 26) {
          Toast({
            message: '地址输入内容过多',
            duration: 1000
          });
          return false
        } else if (/^\d+$/.test(this.familyAddressInfo.address)) {
          Toast({
            message: '详细地址不能为纯数字',
            duration: 1000
          });
          return false
        } else {
          return true
        }
      },

      _testbranchPhone: function () {
        let vm = this;
        let re = /^[0-9]{0,6}$/
        if (!re.test(vm.corpAddressInfo.branchPhone)) {
          Toast({
            message: '分机号请输入数字',
            duration: 1000
          });
          return false
        } else {
          return true
        }
      },

      postTF() {
        let re = /^[0-9]{6}$/;
        let vm = this;
        if (this.$tools._isNullOrEmpty(vm.familyAddressInfo.postCode)) {
          Toast({
            message: '请输入家庭邮政编码',
            duration: 1000
          });
          return false
        } else if (this.familyAddressInfo.postCode.length > 6) {
          this.familyAddressInfo.postCode = this.familyAddressInfo.postCode.substr(0, 6)
        } else if (!re.test(this.familyAddressInfo.postCode)) {
          Toast({
            message: '家庭邮政编码格式不对',
            duration: 1000
          });
          return false
        } else {
          return true
        }
      },
      precodeTF() {
        let vm = this;
        if (this.$tools._isNullOrEmpty(vm.familyAddressInfo.preCode)) {
          Toast({
            message: '请输入家庭电话区号',
            duration: 1000
          });
          return false
        } else if (!/^\d{3,4}$/.test(this.familyAddressInfo.preCode)) {
          Toast({
            message: '区号仅支持3-4位数字',
            duration: 1000
          });
          return false
        } else {
          return true
        }
      },
      phoneTF() {
        let re = /^\d{7,8}$/;
        let vm  = this;
        if (this.$tools._isNullOrEmpty(vm.familyAddressInfo.familyPhone)) {
          Toast({
            message: '请输入家庭电话号码',
            duration: 1000
          });
          return false
        } else if (!re.test(this.familyAddressInfo.familyPhone)) {
          Toast({
            message: '座机号码为7-8位数字',
            duration: 1000
          });
          return false
        } else {
          return true
        }
      },
      relativephoneTF() {
        let value = this.familyAddressInfo.relationPhone;
        if (value.length === 0) {
          Toast({
            message: '请输入手机号码',
            duration: 1000
          });
          return false
        } else if (!isvalidPhone(value)) {
          Toast({
            message: '请输入正确的11位手机号码',
            duration: 1000
          });
          return false
        } else if (value === this.allUserData.tel) {
          //手机号不能和链接手机号重复，也不能和直系亲属手机号重复
          Toast({
            message: '手机号码不能和生成二维码手机号码重复',
            duration: 1000
          });
          return false
        } else if (value === this.detailRelationPhone) {
          Toast({
            message: '手机号码不能和个人信息中联系人手机号码重复',
            duration: 1000
          });
          return false
        } else {
          return true
        }

      },
      familyRelationTF() {
        let vm = this;
        if (this.$tools._isNullOrEmpty(vm.familyAddressInfo.familyRelation)) {
          Toast({
            message: '请选择直系亲属的关系',
            duration: 1000
          })
          return false
        } else {
          return true
        }
      },
      // 公司地址校验
      addressTFC() {
        let vm = this;
        if (this.$tools._isNullOrEmpty(vm.corpAddressInfo.address)) {
          Toast({
            message: '请输入公司具体地址',
            duration: 1000
          });
          return false
        } else if (this.corpAddressInfo.address.length !== 0 && this.corpAddressInfo.address.length < 4) {
          Toast({
            message: '地址输入内容过少',
            duration: 1000
          });
          return false
        } else if (this.corpAddressInfo.address.length > 26) {
          Toast({
            message: '地址输入内容过多',
            duration: 1000
          });
          return false
        } else if (this._test0E0F(this.corpAddressInfo.address) > 55) {
          Toast({
            message: '公司详细地址不能超过55位',
            duration: 1000
          });
          return false
        } else if (/^\d+$/.test(this.corpAddressInfo.address)) {
          Toast({
            message: '详细地址不能为纯数字',
            duration: 1000
          });
          return false
        } else {
          return true
        }
      },
      postTFC() {

        let vm = this;
        let rec = /^[0-9]{6}$/;
        if (this.$tools._isNullOrEmpty(vm.corpAddressInfo.postCode)) {
          Toast({
            message: '请输入公司邮政编码',
            duration: 1000
          });
          return false
        } else if (this.corpAddressInfo.postCode.length > 6) {
          this.corpAddressInfo.postCode = this.corpAddressInfo.postCode.substr(0, 6)
        } else if (!rec.test(this.corpAddressInfo.postCode)) {
          Toast({
            message: '公司邮政编码格式不对',
            duration: 1000
          });
          return false
        } else {
          return true
        }
      },
      precodeTFC() {
        let vm = this;
        if (this.$tools._isNullOrEmpty(vm.corpAddressInfo.preCode)) {
          Toast({
            message: '请输入公司电话区号',
            duration: 1000
          });
          return false
        } else if (!/^\d{3,4}$/.test(this.corpAddressInfo.preCode)) {
          Toast({
            message: '区号仅支持3-4位数字',
            duration: 1000
          });
          return false
        } else {
          return true
        }
      },
      phoneTFC() {
        let re = /^\d{7,8}$/;
        let vm = this;
        if (this.$tools._isNullOrEmpty(vm.corpAddressInfo.corpPhone)) {
          Toast({
            message: '请输入公司电话号码',
            duration: 1000
          });
          return false
        } else if (!re.test(this.corpAddressInfo.corpPhone)) {
          Toast({
            message: '座机号码为7-8位数字',
            duration: 1000
          });
          return false
        } else {
          return true
        }
      },
    }
  }
</script>

