          flex-grow: 0;
          margin-left: 10px;
        }
      }

    }

    .homeEmailClass {
      height: 48px;
      padding-left: .14rem;
      padding-right: .14rem;

      .min-wrapper {
        height: 48px;
        padding-left: 10px;
        padding-right: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .title {
          width: 80px;
          flex-shrink: 0;
          flex-grow: 0;
        }

        input {
          height: 25px;
          border: none;
          outline: none;
        }

        .zone-num {
          width: 0.8rem;
          margin-left: 2px;
          flex-shrink: 0;
          flex-grow: 0;
        }

        .phone-num-class {
          width: 2.5rem;
          flex-shrink: 0;
          flex-grow: 0;
          padding-left: 10px;
        }
      }

    }

    .hengstyle {
      padding-left: 10px;
    }

    .detail-address {
      padding-top: .08rem;
      padding-left: .14rem;
      padding-right: .14rem;

      .title {
        line-height: .8rem;
      }

      .link {
        line-height: .8rem;
        text-align: center;
      }

      #phone-style {
        .el-form-item__label {
          width: 200px;
        }
      }
    }

    .next-btn {
      text-align: center;
      margin-top: .5rem;
      padding-bottom: .2rem;

      button {
        width: 5rem;
        height: .8rem;
        border: 2px solid #fff;
        color: #fff;
        background: #3174f6;
        margin: 0 auto;
        border-radius: 40px;
        outline: none;
      }
    }

    .commont input {
      color: #606266;
    }
  }

</style>



