      _test0E0F: function (str) {
        let temp = "";
        let reChn = /^[\u4e00-\u9fa5]/;
        let reChnTwo = /^[\u4e00-\u9fa5]{2}/;
        let reChnThird = /[\u4e00-\u9fa5]+/;
        let reChnFourth = /[\u4e00-\u9fa5]*/;
        let length = this._getLengthOfString(str);
        let list = new Array();
        if (str.length == 1) {
          if (reChn.test(str)) {
            length += 2;
          }
        }
        for (let i = 0; i < str.length - 1; i++) {
          let s = str.substring(i, i + 2);
          /*if (s.matches("[a-zA-Z0-9]{2}") && temp.matches("[a-zA-Z0-9]*")) {
                          temp += str.substring(i + 1, i + 2);
                          if (i == str.length() - 2) {
                              list.add(temp);
                              temp = "";
                          }
          } else */
          if (reChnTwo.test(s) && reChnFourth.test(temp)) {
            temp += str.substring(i + 1, i + 2);
            if (i == str.length - 2) {
              list.push(temp);
              temp = "";
              length += 2;
            }
          } else {
            //list.add(temp);
            if (reChnThird.test(str.substring(i, i + 1))) {
              length += 2;
            }
            temp = str.substring(i + 1, i + 2);
          }
        }
        return length;

      },

