      _testEmail: function () {
        let vm = this;
        let regEmail = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
        if (vm.detailInformation.email === '') {
          Toast({
            message: '请输入邮箱',
            duration: 1000
          })
          return false
        } else if (!regEmail.test(vm.detailInformation.email)) {
          Toast({
            message: '请输入正确的邮箱',
            duration: 1000
          })
          return false
        } else if (this._test0E0F(vm.detailInformation.email) > 30) {
          Toast({
            message: '电子邮箱输入不能超过30位',
            duration: 1000
          })
          return false
        } else {
          return true
        }
      },

      _getPostionInfo: function () {
        _ui.showLoading("");
        let vm = this;
        let work = {
          "IndustryId": this.detailInformation.industryType,
        }
        console.log(work);
        vm.$Rest._post(this.$Rest.api._work, work)
          .then(function (resp) {
            _ui.hideLoading();
            if (resp.data.records) {
              vm.post = resp.data.records;
            }
          })
          .catch(function (err) {
            _ui.hideLoading();
            console.log(err);
            _ui.showToast(err);
          });
      },

      _testCancel: function () {
        let vm = this;
        // vm._scrollMove();
      },

      _handleVisibleChange: function (isVisible) {
        let vm = this;
        if (!vm.isOpen && !isVisible) {
          // vm._scrollMove();
        }
        if (vm.isOpen) {
          vm._scrollStop();
          vm.isOpen = false;
        }
      },

      _handleconfirm(data) {
        console.log(data);
        var curr_date = data.getDate();
        var curr_month = data.getMonth() + 1;
        var curr_year = data.getFullYear();
        String(curr_month).length < 2 ? (curr_month = "0" + curr_month) : curr_month;
        String(curr_date).length < 2 ? (curr_date = "0" + curr_date) : curr_date;
        var yyyyMMdd = curr_year + "" + curr_month + "" + curr_date;
        let date = yyyyMMdd;
        console.log(date);
        let vm = this;
        // this._scrollMove();
        if (this.type == 'start') {
          this.detailInformation.unitStart = date;
          if (!this._testUnitStart()) {
            this.detailInformation.unitStart = '';
          }
        }
      },

      _substringCarNumber: function () {
        let vm = this;
        let carFirst = vm.detailInformation.carNo.substr(0, 1);
        let carSecond = vm.detailInformation.carNo.substr(1, vm.detailInformation.carNo.length - 1);
        vm.carNo = carFirst;
        vm.detailInformation.carNo = carSecond;

      },

      _getIndustryInfo: function () {
        let vm = this;
        _ui.showLoading("");
        http._post("/J_BEFP_010_0001", "")
          .then(function (resp) {
            console.log(resp.date);
            cb.result = true;
            cb.date = resp;
            vm.employee = cb;

          })
          .catch(function (err) {
            console.log(err);
            cb.result = false;
            cb.message = "请求异常";
            _ui.showToast(cb.message);
          })
      },


    }
  }
</script>

