<template>
  <main style="position: relative;">
    <div class="lock">
      <img src="../assets/auto/suo.png" alt="">
    </div>
    <p class="remain-text">将向您尾号<span>{{ allUserData.tel.substr(7, 12) }}</span>的手机号发送验证码：</p>
    <div v-on:click="getAuth" class="get-auth" :class="{'get-auth-timer':showCount}">
      <span>点击获取</span><span v-show="showCount">{{ '('+ count +'s)' }}</span>
    </div>

    <div class="input-text">
      <input class="input-num" v-model="input" type="tel">
    </div>
    <div class="terms-wrapper">
      <div class="terms-one">
        <el-checkbox class="img-left" v-model="checked1"></el-checkbox>
        <div class="terms">
          本人已阅读并同意
          <span @click="goCredit()" class="pink">《征信授权及条款》</span><span class="pink">·</span><span class="pink"
                                                                                                  @click="goConstitution()">《交通银行太平洋信用卡及章程》</span><span
          class="pink">·</span><span @click="goContract()" class="pink">《交通银行太平洋信用卡领用合约》</span>
        </div>
      </div>
      <div class="terms-two">
        <el-checkbox class="img-left" v-model="checked2"></el-checkbox>
        <div class="terms">
          本人承托仅为中国税收居民，非美国个人以及其他国家（地区）税收居民（即非居民）
          <span @click="goRevenue()" class="pink">点击查看<img src=""></span>
        </div>
      </div>
    </div>
    <div class="next-btn">
      <button v-on:click="infoNext">
        下一步填写基本信息
      </button>
    </div>
    <credit></credit>
    <constitution></constitution>
    <contract></contract>
    <revenue></revenue>
  </main>
</template>

<script>
  import {MessageBox, Toast, Indicator} from 'mint-ui';
  import credit from './agreement/credit';
  import constitution from './agreement/constitution';
  import contract from './agreement/contract';
  import revenue from './agreement/revenue';

  export default {
    name: '',
    data() {
      return {
        checked1: false,
        checked2: false,
        count: '60',
        showCount: false,
        input: ''
      }
    },
    components: {
      credit,
      constitution,
      contract,
      revenue
    },
    computed: {

      allUserData() {
        return this.$store.state.allUserData
      },
      authData() {
        return {
          "flag": "0",
          "phoneNo": this.allUserData.tel,
          "orderId": this.allUserData.orderId
        }
      },
      authTF() {
        return {
          "flag": "1",
          "phoneNo": this.allUserData.tel,
          "orderId": this.allUserData.orderId,
          "smsAuthCode": this.input
        }
      }
    },
    methods: {
      goRevenue() {
        this.$store.commit('SHOW_REVENUE', false)
      },
      goContract() {
        this.$store.commit('SHOW_CONTRACT', false)
      },
      goConstitution() {
        this.$store.commit('SHOW_CONSTITUTION', false)
      },
      goCredit() {
        this.$store.commit('SHOW_CREDIT', false)
      },
      getAuth() {
        let _this = this;
        //倒计时
        if (this.showCount) {
          return
        }
        _this.showCount = true;
        let setTimeData = setInterval(setTime, 1000);

        function setTime() {
          if (_this.count > 1) {
            _this.count--;
          } else {
            _this.showCount = false;
            _this.count = '60';
            clearInterval(setTimeData);
          }
        }

        //防止重复提交
        let prevent = false;
        if (prevent) {
          return
        }
        prevent = true;
        this.$axios.post(this.$Rest.api._getAuth, this.authData)
          .then((res) => {
            if (res.data.rtnCode === '200') {
              Toast('已发送，注意接收！')
            } else if (res.data.rtnCode === '300') {
              Toast(res.data.rtnMsg)
            } else {
              Toast(res.data.rtnMsg)
            }
            prevent = false;
          }).catch(function (err) {
          MessageBox('注意', '网络错误！');
          prevent = false;
          _this.count = '60';
          clearInterval(setTimeData);
          _this.showCount = false;
        })
      },
      infoNext() {

        if (!this.checked1) {
          MessageBox('注意', '请选择同意条款！');
          return
        }
        if (!this.checked2) {
          MessageBox('注意', '请选择同意条款！');
          return
        }

        if (this.input.trim() === '') {
          Toast('请输入验证码！')
          return
        }
        Indicator.open();
        this.$axios.post(this.$Rest.api._sendAuth, this.authTF)
          .then((res) => {
            Indicator.close();
            let data = res.data;
            if (data.rtnCode === '200') {
              this.$store.commit('changeToken', data.toKen);
              if (this.allUserData.qrCodeType === '0' || this.allUserData.qrCodeType === '2') {
                this.$store.commit('changeShow', 'content');
              } else {
                this.$store.commit('changeShow', 'add');
              }
            } else if (data.rtnCode === '300') {
              Toast('验证码超时，请重新发送！')
            } else {
              Toast(res.data.rtnMsg)
            }
          }).catch(function (err) {
          Indicator.close();
          MessageBox('注意', '网络错误！');
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  * {
    box-sizing: border-box;
  }

  main {
    padding-top: 90px;
    padding-bottom: 20px;
    min-height: 100%;
    background: linear-gradient(#5b92ef, #2d6cd5);
  }

  .lock {
    text-align: center;

    img {
      width: 79px;
      height: 88px;
    }
  }

  .remain-text {
    text-align: center;
    color: #fff;
    font-size: 16px;
    padding-bottom: 15px;
  }

  .get-auth {
    color: #ffafaf;
    text-align: center;
    padding-bottom: 6px;
    border-bottom: 1px solid #bacdf5;
    width: 75px;
    margin: 0 auto;
  }

  .get-auth-timer {
    color: #ddd;
  }

  .input-text {
    margin-top: 30px;
    text-align: center;
  }

  .input-text .input-num {
    width: 5.28rem;
    height: 41px;
    background: #93b2e8;
    border: 2px solid #fff;
    border-radius: 9px;
    padding-left: 8px;
    outline: none;
    color: #fff;
  }

  .terms-wrapper {
    width: 100%;
    padding-left: 35px;
    padding-right: 35px;

    .terms-one {
      margin-top: 65px;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      font-size: 12px;
      color: #fff;

      .img-left {
        flex-grow: 0;
        flex-shrink: 0;
      }

      .terms {
        padding-left: 15px;

        .pink {
          color: #d397cb;
        }
      }
    }

    .terms-two {
      margin-top: 25px;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      font-size: 12px;
      color: #fff;

      .img-left {
        flex-grow: 0;
        flex-shrink: 0;
      }

      .terms {
        padding-left: 15px;

        .pink {
          color: #d397cb;
        }
      }
    }
  }

  .next-btn {
    text-align: center;
    margin-top: 67px;

    button {
      width: 275px;
      height: 40px;
      border: 2px solid #fff;
      color: #fff;
      background: none;
      margin: 0 auto;
      border-radius: 40px;
      outline: none;
    }
  }
</style>





