        // this.$axios.get('http://localhost:8080/static/json/del.json',  {
        this.$axios.post('/J_BEFP_010_0005', {
          "token": this.$store.state.toKen,
          "orderId": this.allUserData.orderId,
          "pageIndex": "4",
          "D_testFlag": "1", //1是删除，2是下载
          "D_fileId": this.imgG[index][0]
        })
          .then((res) => {
            if (res.data.rtnCode == '200') {
              this.imgG.splice(index, 1);
              Toast('删除成功！')
              if (this.imgG.length == 0) {
                this.tmdG = false;
              }
              sessionStorage.setItem("imgG", JSON.stringify(this.imgG))
            } else {
              MessageBox('注意', '删除失败');
            }

