                _this.imgContrary = res.data.fileInfo;
              } else {
                //图片没到
                _this.imgContrary = './static/img/picture/contrary.png'
              }
            })
            .catch(function (err) {
              //图片没到
              _this.imgContrary = './static/img/picture/contrary.png'
              MessageBox('注意', '网络错误！');
            })
        }

        //发送请求获取头像
        this.imgHId = sessionStorage.getItem('imgHId');
        if (this.imgHId) {
          // this.$axios.get('http://localhost:8080/static/json/download.json', {
          this.$axios.post('/J_BEFP_010_0005', {
            "token": this.$store.state.toKen,
            "orderId": this.allUserData.orderId,
            "pageIndex": "4",
            "D_testFlag": "2", //1是删除，2是下载
            "D_fileId": this.imgHId
          })
            .then((res) => {
              if (res.data.rtnCode == '200') {
                _this.tmdH = true;
                //图片到了
                _this.imgH = res.data.fileInfo;
              } else {
                //图片没到
                _this.imgH = './static/img/picture/camera.png'
                _this.tmdH = false
              }
            })
            .catch(function (err) {
              //图片没到
              _this.imgH = './static/img/picture/camera.png'
              _this.tmdH = false
              MessageBox('注意', '网络错误！');
            })
        }

        //发送请求获取纸质合约
        this.imgZId = sessionStorage.getItem('imgZId');
        if (this.imgZId) {
          // this.$axios.get('http://localhost:8080/static/json/download.json', {
          this.$axios.post('/J_BEFP_010_0005', {
            "token": this.$store.state.toKen,
            "orderId": this.allUserData.orderId,
            "pageIndex": "4",
            "D_testFlag": "2", //1是删除，2是下载
            "D_fileId": this.imgZId
          })
            .then((res) => {
              if (res.data.rtnCode == '200') {
                //图片到了
                _this.tmdZ = true;
                _this.imgZ = res.data.fileInfo;
              } else {
                //图片没到
                _this.imgZ = './static/img/picture/camera.png'
                _this.tmdZ = false
              }
            })
            .catch(function (err) {
              //图片没到
              _this.imgZ = './static/img/picture/camera.png'
              _this.tmdZ = false
              MessageBox('注意', '网络错误！');
            })
        }
        //工作证明文件下载
        if (sessionStorage.getItem('imgG')) {
          _this.imgG = JSON.parse(sessionStorage.getItem('imgG'))
        }
        if (_this.imgG.length != '0') {

          _this.tmdG = true;

          _this.imgG.forEach((item, index) => {
            // this.$axios.get('http://localhost:8080/static/json/download.json', {
            _this.$axios.post('/J_BEFP_010_0005', {
              "token": this.$store.state.toKen,
              "orderId": this.allUserData.orderId,
              "pageIndex": "4",
              "D_testFlag": "2", //1是删除，2是下载
              "D_fileId": item[0]
            })
              .then((res) => {

                if (res.data.rtnCode == '200') {
                  _this.imgG[index][1] = res.data.fileInfo;
                  sessionStorage.setItem('imgG', JSON.stringify(_this.imgG))
                } else {
                  _this.imgG.splice(0, 4);
                  sessionStorage.setItem('imgG', JSON.stringify(_this.imgG))
                  MessageBox('注意', res.data.rtnMsg);
                  if (_this.imgG.length == '0') {
                    _this.tmdG = false;
                  }
                }

                if (_this.imgG.length == '0') {
                  _this.workPictureTitle = '请拍摄工作证明文件'
                } else if (_this.imgG.length == '4') {
                  _this.workPictureTitle = '拍摄完成'
                } else {
                  _this.workPictureTitle = '点击空白处继续拍摄，最多拍摄4张'
                }

                _this.showCloseG()//显示g6
              })
              .catch(function (err) {
                _this.imgG.splice(0, 4);
                sessionStorage.setItem('imgG', JSON.stringify(_this.imgG))
                MessageBox('注意', '网络错误！');
                if (_this.imgG.length == '0') {
                  _this.tmdG = false;
                }

                if (_this.imgG.length == '0') {
                  _this.workPictureTitle = '请拍摄工作证明文件'
                } else if (_this.imgG.length == '4') {
                  _this.workPictureTitle = '拍摄完成'
                } else {
                  _this.workPictureTitle = '点击空白处继续拍摄，最多拍摄4张'
                }

                _this.showCloseG()//显示g7
              })
          })
        }

        //other图片下载
        if (sessionStorage.getItem('imgOther')) {
          _this.imgOther = JSON.parse(sessionStorage.getItem('imgOther'))
        }
        if (_this.imgOther.length != '0') {
          _this.tmdO = true;
          _this.imgOther.forEach((item, index) => {
            // _this.$axios.get('http://localhost:8080/static/json/download.json', {
            _this.$axios.post('/J_BEFP_010_0005', {
              "token": this.$store.state.toKen,
              "orderId": this.allUserData.orderId,
              "pageIndex": "4",
              "D_testFlag": "2", //1是删除，2是下载
              "D_fileId": item[0]
            })
              .then((res) => {
                if (res.data.rtnCode == '200') {
                  let imgzj = JSON.parse(JSON.stringify(_this.imgOther))
                  imgzj[index][1] = res.data.fileInfo
                  // _this.imgOther[index][1] = res.data.fileInfo;

                  sessionStorage.setItem('imgOther', JSON.stringify(imgzj))
                  getother();

                } else {

                  _this.imgOther.splice(0, 4);
                  sessionStorage.setItem('imgOther', JSON.stringify(_this.imgOther))
                  MessageBox('注意', res.data.rtnMsg);
                  if (_this.imgOther.length == '0') {
                    _this.tmdO = false;
                  }
                }

                if (_this.imgOther.length == '0') {
                  _this.otherPictureTitle = '请拍摄其他文件'
                } else if (_this.imgOther.length == '4') {
                  _this.otherPictureTitle = '拍摄完成'
                } else {
                  _this.otherPictureTitle = '点击空白处继续拍摄，最多拍摄4张'
                }

                _this.showCloseO()//判断显示6

              })
              .catch(function (err) {
                _this.imgOther.splice(0, 4);
                sessionStorage.setItem('imgOther', JSON.stringify(_this.imgOther))
                MessageBox('注意', '网络错误！');
                if (_this.imgOther.length == '0') {
                  _this.tmdO = false;
                }

                if (_this.imgOther.length == '0') {
                  _this.otherPictureTitle = '请拍摄其他文件'
                } else if (_this.imgOther.length == '4') {
                  _this.otherPictureTitle = '拍摄完成'
                } else {
                  _this.otherPictureTitle = '点击空白处继续拍摄，最多拍摄4张'
                }

                _this.showCloseO()//判断显示7
              })
          })
        }

        function getother() {
          if (sessionStorage.getItem('imgOther')) {
            _this.imgOther = JSON.parse(sessionStorage.getItem('imgOther'))
          }
        }
      },
      openCammer(who) {
        /*
           上传图片的时候需要这两个字段
           qrCodeType
           fileBlockIndex
       */
        let _this = this;
        if (who == 'f') {

          this.$devices._takePhoto(function (file, base64, formData) {
            if (base64) {
              _this.imgFront = base64;

              _this.$axios.post('/J_BEFP_010_0001', _this.pictureData1)
                .then((res) => {
                  if (res.data.rtnCode === '200') {
                    Toast(res.data.rtnMsg);
                    _this.imgFrontId = res.data.D_fileId
                    sessionStorage.setItem("imgFrontId", _this.imgFrontId)
                  } else {
                    MessageBox('注意', '上传失败');
                    _this.imgFront = './static/img/picture/front.png'
                  }
                })
                .catch(function (err) {
                  MessageBox('注意', '网络错误！');
                  _this.imgFront = './static/img/picture/front.png'
                })
            } else {
              _this.imgFront = './static/img/picture/front.png'
            }
          }, 0.2);


        }

        if (who == 'c') {
          this.$devices._takePhoto(function (file, base64, formData) {
            if (base64) {
              _this.imgContrary = base64
              _this.$axios.post('/J_BEFP_010_0001', _this.pictureData2)
                .then((res) => {
                  if (res.data.rtnCode == '200') {
                    Toast(res.data.rtnMsg)
                    _this.imgContraryId = res.data.D_fileId
                    sessionStorage.setItem("imgContraryId", _this.imgContraryId)
                  } else {
                    MessageBox('注意', '上传失败');
                    _this.imgContrary = './static/img/picture/contrary.png'
                  }
                })
                .catch(function (err) {
                  MessageBox('注意', '网络错误！');
                  _this.imgContrary = './static/img/picture/contrary.png'
                })
            } else {
              _this.imgContrary = './static/img/picture/contrary.png'
            }
          }, 0.2);
        }

        if (who == 'h') {
          this.$devices._takePhoto(function (file, base64, formData) {
            if (base64) {
              _this.imgH = base64
              _this.tmdH = true;

              _this.$axios.post('/J_BEFP_010_0001', _this.pictureData3)
                .then((res) => {
                  if (res.data.rtnCode == '200') {
                    _this.imgHId = res.data.D_fileId
                    sessionStorage.setItem("imgHId", _this.imgHId)
                    Toast(res.data.rtnMsg)
                  } else {
                    MessageBox('注意', '上传失败');
                    _this.imgH = './static/img/picture/camera.png'
                    _this.tmdH = false
                  }
                })
                .catch(function (err) {
                  MessageBox('注意', '网络错误！');
                  _this.imgH = './static/img/picture/camera.png'
                  _this.tmdH = false
                })
            } else {
              _this.imgH = './static/img/picture/camera.png'
              _this.tmdH = false
            }
          }, 0.2);
        }

        if (who == 'z') {
          this.$devices._takePhoto(function (file, base64, formData) {
            if (base64) {
              _this.imgZ = base64
              _this.tmdZ = true;

              _this.$axios.post('/J_BEFP_010_0001', _this.pictureData4)
                .then((res) => {
                  if (res.data.rtnCode == '200') {
                    Toast(res.data.rtnMsg)
                    _this.imgZId = res.data.D_fileId
                    sessionStorage.setItem("imgZId", _this.imgZId)
                  } else {
                    MessageBox('注意', '上传失败');
                    _this.imgZ = './static/img/picture/camera.png'
                    _this.tmdZ = false
                  }
                })
                .catch(function (err) {
                  MessageBox('注意', '网络错误！');
                  _this.imgZ = './static/img/picture/camera.png'
                  _this.tmdZ = false
                })
            } else {
              _this.imgZ = './static/img/picture/camera.png'
              _this.tmdZ = false
            }
          }, 0.2);
        }

        if (who == 'g' && _this.imgG.length < 4) {

          this.$devices._takePhoto(function (file, base64, formData) {
            if (base64) {
              let imgOPA = ['', '']
              imgOPA[1] = base64
              _this.imgG.push(imgOPA)
              if (_this.imgG.length > 4) {
                _this.imgG.length = 4;
              }
              //显示g1
              _this.showCloseG()
              _this.tmdG = true;
              _this.imgG.forEach((item, index) => {
                // _this.$axios.get('http://localhost:8080/static/json/picture.json', {
                _this.$axios.post('/J_BEFP_010_0001', {
                  // "qrCodeType": jiami.enJiaMi(_this.allUserData.qrCodeType),
                  // "fileBlockIndex": jiami.enJiaMi('1'),
                  // "pageIndex": '4',
                  // "orderId": jiami.enJiaMi(_this.allUserData.orderId),
                  // "token": jiami.enJiaMi(_this.$store.state.toKen),
                  // "fileInfo" : jiami.enJiaMi(item[1]),
                  // "fileName": jiami.enJiaMi('Checklist_03-C004-0'+ index +'.jpg'),
                  "qrCodeType": _this.allUserData.qrCodeType,
                  "fileBlockIndex": '1',
                  "pageIndex": '4',
                  "orderId": _this.allUserData.orderId,
                  "token": _this.$store.state.toKen,
                  "fileInfo": item[1],
                  "fileName": 'Checklist_03-C004-0' + index + '.jpg'
                })
                  .then((res) => {
                    if (res.data.rtnCode == '200') {
                      Toast(res.data.rtnMsg)
                      imgOPA[0] = res.data.D_fileId
                      sessionStorage.setItem('imgG', JSON.stringify(_this.imgG))
                    } else {
                      _this.imgG.splice(0, 4);
                      sessionStorage.setItem('imgG', JSON.stringify(_this.imgG))
                      MessageBox('注意', '上传失败');
                      if (_this.imgG.length == '0') {
                        _this.tmdG = false;
                      }
                    }

                    if (_this.imgG.length == '0') {
                      _this.workPictureTitle = '请拍摄工作证明文件'

                    } else if (_this.imgG.length == '4') {
                      _this.workPictureTitle = '拍摄完成'

                    } else {
                      _this.workPictureTitle = '点击空白处继续拍摄，最多拍摄4张'

                    }

                    //显示g2
                    _this.showCloseG()

                  })
                  .catch(function (err) {
                    MessageBox('注意', '网络错误！');
                    _this.imgG.splice(0, 4);
                    _this.tmdG = false;

                    if (_this.imgG.length == '0') {
                      _this.workPictureTitle = '请拍摄工作证明文件'

                    } else if (_this.imgG.length == '4') {
                      _this.workPictureTitle = '拍摄完成'
                    } else {
                      _this.workPictureTitle = '点击空白处继续拍摄，最多拍摄4张'
                    }

                    //显示g3
                    _this.showCloseG()
                  })
              })
            }
          }, 0.2);
        }

        if (who == 'o' && _this.imgOther.length < 4) {
          this.$devices._takePhoto(function (file, base64, formData) {
            if (base64) {
              let imgOPAD = ['', '']
              let allCamera = []
              imgOPAD[1] = base64
              _this.imgOther.push(imgOPAD)
              if (_this.imgOther.length > 4) {
                _this.imgOther.length = 4;
                return
              }

              _this.showCloseO()//判断显示3

              _this.tmdO = true;
              _this.imgOther.forEach((item, index) => {
                // _this.$axios.get('http://localhost:8080/static/json/picture.json', {
                _this.$axios.post('/J_BEFP_010_0001', {

                  // "fileInfo" : jiami.enJiaMi(item[1]),
                  // "fileName": jiami.enJiaMi('Checklist_13-L001-0'+index+'.jpg'),

                  "qrCodeType": _this.allUserData.qrCodeType,
                  "fileBlockIndex": '1',
                  "pageIndex": '4',
                  "orderId": _this.allUserData.orderId,
                  "token": _this.$store.state.toKen,
                  "fileInfo": item[1],
                  "fileName": 'Checklist_13-L001-0' + index + '.jpg',
                })
                  .then((res) => {
                    if (res.data.rtnCode == '200') {
                      Toast(res.data.rtnMsg)
                      imgOPAD[0] = res.data.D_fileId
                      sessionStorage.setItem('imgOther', JSON.stringify(_this.imgOther))
                    } else {
                      _this.imgOther.splice(0, 4);
                      sessionStorage.setItem('imgOther', JSON.stringify(_this.imgOther))
                      MessageBox('注意', '上传失败');
                      if (_this.imgOther.length == '0') {
                        _this.tmdO = false;
                      }
                    }

                    if (_this.imgOther.length == '0') {
                      _this.otherPictureTitle = '请拍摄其他文件'
                    } else if (_this.imgOther.length == '4') {
                      _this.otherPictureTitle = '拍摄完成'
                    } else {
                      _this.otherPictureTitle = '点击空白处继续拍摄，最多拍摄4张'
                    }

                    _this.showCloseO()//判断显示4
                  })
                  .catch(function (err) {
                    MessageBox('注意', '网络错误！');
                    _this.imgOther.splice(0, 4);
                    sessionStorage.setItem('imgOther', JSON.stringify(_this.imgOther))
                    if (_this.imgOther.length == '0') {
                      _this.tmdO = false;
                    }

                    _this.showCloseO()//判断显示5

                    if (_this.imgOther.length == '0') {
                      _this.otherPictureTitle = '请拍摄其他文件'
                    } else if (_this.imgOther.length == '4') {
                      _this.otherPictureTitle = '拍摄完成'
                    } else {
                      _this.otherPictureTitle = '点击空白处继续拍摄，最多拍摄4张'
                    }
                  })
              })
            }
          }, 0.2);

        }


      },

      infoNext() {
        let _this = this;
        // this.$store.commit('changeShow', 'verify');
        if (this.imgFront == './static/img/picture/front.png') {
          Toast('请上传身份证正面！')
          return
        }
        if (this.imgContrary == './static/img/picture/contrary.png') {
          Toast('请上传身份证反面！')
          return
        }
        if (this.imgH == './static/img/picture/camera.png') {
          Toast('请拍摄本人头像！')
          return
        }
        if (this.imgZ == './static/img/picture/camera.png') {
          Toast('拍摄纸质合约！')
          return
        }
        //等于1的时候不需要判断，1 通过 2没有通过
        if (this.result !== '1' && this.imgG.length == "0") {
          Toast('请拍摄工作证明文件！')
          return
        }

        this.$axios.post('/J_BEFP_010_0001', {
          "orderId": _this.allUserData.orderId,
          "token": _this.$store.state.toKen,
          "pageIndex": "-1"
        })
          .then((res) => {
            if (res.data.rtnCode == '200') {
              Toast(res.data.rtnMsg)
              //跳转到修改
              this.$store.commit('changeShow', 'verify');
            } else {
              MessageBox('注意', res.data.rtnMsg);
            }
          })
          .catch(function (err) {
            MessageBox('注意', '网络错误！');
          })
      },
    }
  }
</script>

