import Vue from 'vue'
import Router from 'vue-router'
import Check from '@/components/check/CheckView'
import BasicInformation from '@/components/basic/BasicInformationView'
import Mail from '@/components/mail/MailView'
import Detail from '@/components/detail/DetailView'
import Extra from '@/components/extra/ExtraView'
import Picture from '@/components/picture/Picture'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'basic'
    },
    {
      path: '/check',
      name: 'check',
      component: Check,
    },
    {
      path:'/basic',
      name:'basic',
      component:BasicInformation
    },
    {
      path:'/mail',
      name:'mail',
      component:Mail,
    },
    {
      path:'/detail',
      name:'detail',
      component:Detail
    },
    {
      path:'/picture',
      name:'picture',
      component:Picture
    },
    {
      path:'/extra',
      name:'extra',
      component:Extra
    },
  ],
  scrollBehavior:function (to,from,savedPosition) {

    if (savedPosition) {
      return savedPosition
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  }
})



