import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        showCredit: true,
        showConstitution: true,
        showContract: true,
        showRevenue: true,

        toKen: '',
        showInformation: 'content',

        allUserData: {},
        isLogin: false,
        isFirstApply: false,
      
        imgFront: './static/img/picture/front.png',
        imgContrary: './static/img/picture/contrary.png',
        imgH: './static/img/picture/camera.png',
        tmdH: false,
        imgZ: './static/img/picture/camera.png',
        tmdZ: false,
        imgG: [],
        tmdG: false,
        imgO: [],
        tmdO: false
    },
    getters: {
        isLogin(state) {
            if(!state.isLogin) {
                state.isLogin = JSON.parse(sessionStorage.getItem('isLogin'));
                state.username = JSON.parse(sessionStorage.getItem('allUserData'));
            }
            return state.allUserData;
        }
    },
    mutations: {
        changeShow (state, isShow) {
            state.showInformation = isShow
        },
        changeToken (state, toKen) {
            state.toKen = toKen
        },
        familyData (state, familyAddressInfo) {
            state.familyAddressInfo = familyAddressInfo
        },
        imgFront(state, imgFront){
            state.imgFront = imgFront
        },
        changeisFirstApply (state, isFirstApply) {
            state.isFirstApply = isFirstApply
        },
        ALL_USER_DATA(state, allUserData) {
            state.allUserData = allUserData;
        },
        REMOVE_USERDATA(state) {
            state.allUserData = {};
        },
        SHOW_CREDIT (state, isShow) {
            state.showCredit = isShow
        },
        SHOW_CONSTITUTION (state, isShow) {
            state.showConstitution = isShow
        },
        SHOW_CONTRACT (state, isShow) {
            state.showContract = isShow
        },
        SHOW_REVENUE (state, isShow) {
            state.showRevenue = isShow
        },
    }
})



