import crypto from 'crypto'

export default {
    //解密
    deJiaMi(data) {
        let key = "751f621ea5c8f930";
        let iv = "1234569987654321";
        let vm = this;
        let deData = vm._aesDecrypt(key, iv,  data)
        return JSON.parse(deData)
    },
    //加密
    enJiaMi(data) {
      let key = "751f621ea5c8f930";
      let iv = "1234569987654321";
      let vm = this;
      return vm._aesEncrypt(key, iv, JSON.stringify(data))
    },
  _aesEncrypt: function (key, iv, data) {
    let cipher = crypto.createCipheriv('aes-128-cbc', key, iv);
    // console.log(crypto);
    let crypted = cipher.update(data, 'utf8', 'binary');
    crypted += cipher.final('binary');
    crypted = new Buffer(crypted, 'binary').toString('base64');
    
    return crypted;
  },

  _aesDecrypt: function (key, iv, crypted) {
    crypted = new Buffer(crypted, 'base64').toString('binary');
    // console.log(crypto);
    let decipher = crypto.createDecipheriv('aes-128-cbc', key, iv);
    let decoded = decipher.update(crypted, 'binary', 'utf8');
    decoded += decipher.final('utf8');
    return decoded;
  },

  _handlerTel: function (hostName, hostNum, partNum, hostIsSH, partIsSH) {
    let vm = this;

    let params = {
      hostname: hostName,
      hostnumber: hostNum,
      partname: "",
      partnumber: partNum,
      exaid: "15",
      rectype: "",
      pccserid: ""
    };
    params.hostnumber = hostIsSH ? params.hostnumber : "0" + params.hostnumber;
    params.partnumber = partIsSH ? params.partnumber : "0" + params.partnumber;
    return vm._post(this.api._call, params)
    }
}



