<template>
  <div class="main_content" id="basic_id">
    <div class="check-top"></div>
    <div class="basic_color">
      <mt-field style="background: #ddd;" @blur.native.capture="_chnChangeEng(basicInformation.chnName)" label="中文姓名:"
                type="text" class="label_text basic-change-color" placeholder="请输入您的中文姓名"
                v-model="basicInformation.chnName" :attr="{maxLength:30}" :readonly="true"></mt-field>
      <mt-field @blur.native.capture="_testEngName()" label="拼音/英文姓名:" class="label_text" placeholder="请输入您的英文姓名"
                :readonly="hadinfor === true" v-model="basicInformation.engName" style="background:white;"></mt-field>
      <mt-field @blur.native.capture="_testIdno(basicInformation.idNo)" label="证件号码:" class="label_text"
                placeholder="请输入您的证件号码" :readonly="hadinfor === true" v-model="basicInformation.idNo"
                :attr="{maxLength:18}"></mt-field>
      <div class="cert_time">
        <font class="select_cert">证件有效期: </font>
        <input @click='!hadinfor && _openPicker("start")' placeholder="请选择" class="select_tel_basic"
               style="color:#606266" color="#7c99d6" maxlength=10 type="text" v-model="basicInformation.startDate"
               name="start" :readonly="true"></input>
        <img src="../../assets/btn_background_down.jpg" class="img_center" @click='_openPicker("start")'
             disable="true"></img>
        <font style="padding-left: 4px;" class="select" color="#7c99d6">至</font>
        <input @click='!hadinfor &&_openPicker("end")' placeholder="请选择" class="select_tel_basic"
               style="color:#606266; padding-left:2px;" color="#7c99d6" maxlength=10 v-model="basicInformation.endDate"
               name="end" :readonly="true"> </input>
        <img src="../../assets/btn_background_down.jpg" class="img_center" @click='_openPicker("end")'></img>
      </div>

      <div class="checkbox_apply">
        <el-checkbox v-model="checked" class="clause_first" :disabled="hadinfor === true">
          本人同意贵行为审核本人信用卡申请资质,使用本人信息(姓名、证件号、手机号),向相关部门查询本人相关信息。<font style="color: rgb(68, 120, 240);" color="#ff8787"
                                                                    class="click_detail" :readonly="true"
                                                                    @click="_popDetail()">点击查看详情 ＞ </font>
        </el-checkbox>
      </div>
      <div :class="{'next-btn_back': hadinfor}" class="next-btn" ref="next_btn_ref">
        <button v-on:click="_sendBasicInfo">
          下一步:选择卡产品
        </button>
      </div>
      <!-- <mt-button type="default"  class="btn_apply" @click="_sendBasicInfo" >下一步:选择卡产品</mt-button> -->

      <div class="background_bg">
      </div>

      <mt-popup
        v-model="popupBasicVisible"
        position="left"
        popup-transition="popup-fade"
      >
        <template>
          {{popupBasicText}}
        </template>
      </mt-popup>
      <mt-datetime-picker
        ref="picker"
        type="date"
        v-model="pickerValue"
        @confirm="_handleconfirm"
        :startDate="startTime"
        :endDate="endTime"
        class="datetime"
      >
      </mt-datetime-picker>
    </div>
  </div>
</template>

<script>

  import saver from 'file-saver'
  import _ui from './../../base/BaseUi'
  import {MessageBox, Toast} from 'mint-ui';
  import pinyin from '../../components/basic/pinyin'

  export default {
    name: 'basic',
    data() {
      return {
        control_chnName: false,
        control_engName: false,
        control_idNo: false,
        pickerValue: "",
        checked: "",
        popupBasicVisible: false,
        popupBasicText: '',
        isVisible: false,
        isOpen: false,
        hadinfor: false,
        startTime: new Date('1900-01-01'),
        endTime: new Date('2100-12-31'),
        dayCount: '',
        currentDate: '',
        modifiedDate: '',
        username: this.$store.state.allUserData.username,
        basicInformation: {
          pageIndex: "0",
          orderId: this.$store.state.allUserData.orderId,
          custNo: "",
          chnName: "",
          engName: "",
          startDate: "",
          bornDate: "",
          endDate: "",
          idNo: "",
          ip: "",
          mac: "",
          token: this.$store.state.toKen
        },
      }
    },

    directives: {
      focus: function (el, {value}) {
        if (value) {
          el.focus();
        }
      }
    },

    mounted: function () {

      let vm = this;
      let basic_param = {
        pageIndex: "0",
        orderId: this.$store.state.allUserData.orderId,
      };
      _ui.showLoading("");
      //请求基本信息
      this.$axios.post(this.$Rest.api._information, basic_param)
        .then(function (resp) {
          if (resp.data.rtnCode === '200') {

            _ui.hideLoading();

            if (resp.data.records) {
              vm.basicInformation = resp.data.records[0];
              if (resp.data.records[0] != null) {
                vm.hadinfor = true;
              }
            }

            if (vm.hadinfor) {
              vm.checked = true;
            }
          } else {
            vm.$store.commit('changeShow', 'error');
          }
        })
        .catch(function (err) {
          _ui.hideLoading();
        });
      //获取当前日期
      let myDate = new Date();
      let current_date = myDate.getDate();
      let current_month = myDate.getMonth() + 1;
      let current_year = myDate.getFullYear();
      String(current_month).length < 2 ? (current_month = "0" + current_month) : current_month;
      String(current_date).length < 2 ? (current_date = "0" + current_date) : current_date;
      let thisCurrentDate = current_year + "-" + current_month + "-" + current_date;
      vm.currentDate = thisCurrentDate;
      if (vm.username != null && vm.username !== "") {
        vm.basicInformation.chnName = vm._changeCharacter(vm.username);
        vm.basicInformation.engName = pinyin.hz2py(vm.basicInformation.chnName);
      }
    },
    computed: {
      allUserData() {
        return this.$store.state.allUserData
      }
    },
    methods: {
      _chnChangeEng: function (str) {
        let vm = this;
        console.log(str);
        if (str !== null && str !== "") {
          let pinyinname = pinyin.hz2py(str);
          vm.basicInformation.engName = pinyinname;
        } else {
          Toast({
            message: '请输入中文姓名',
            duration: 1000
          })
        }
      },

      _changeCharacter: function (str) {
        let reEng = /^[A-Za-z]/;
        let reChn = /^[\u4e00-\u9fa5]/;
        let reUnCorrectFirst = /^[\.]/;
        let reUnCorrectSecond = /^[\·]/;
        let reUnCorrectThird = /^[\。]/;
        let reUnCorrectFourth = /^[\·]/;
        let trueString = "";
        for (var i = 0; i < str.length; i++) {
          var temp = str.substring(i, i + 1);
          if (reEng.test(temp) ||
            reChn.test(temp) || reUnCorrectFirst.test(temp) || reUnCorrectSecond.test(temp) || reUnCorrectThird.test(temp) || reUnCorrectFourth.test(temp)) {
            trueString += temp;
          }
        }

        if (trueString.indexOf(".") !== -1) {
          str = trueString.replace(/\./g, "·");
        } else if (trueString.indexOf("·") !== -1) {
          str = trueString.replace(/·/g, "·");
        } else if (trueString.indexOf("。") !== -1) {
          str = trueString.replace(/。/g, "·");
        } else {
          str = trueString;
        }
        console.log(trueString);
        return str;
      },

      _popDetail: function () {
        MessageBox({
          title: '',
          message: '本人同意，自本人填写并提交本页所载的本人信息（姓名，证件号，证件类型，手机号）起，授权贵行为审核本人的信用卡或者其他服务申请，向中国人民银行个人信用信息基础数据库和其他合法设立的征信机构查询本人在其中的全部信用信息及信用报告，亦可向相关部门单位了解本人的身份，财产和其他相关信息。',
          confirmButtonText: '知道了'
        });
      },

      _substringBirthday: function () {
        let vm = this;
        let sss = vm.basicInformation.idNo.substr(6, 8);
        vm.basicInformation.bornDate = sss;
        console.log(vm.basicInformation.bornDate);
      },

      _trimEngName: function (str) {
        let vm = this;
        var result;
        result = str.replace(/\s/g, "");
        return result;
      },

      _testEngName: function () {
        let vm = this;
        let engName = vm._trimEngName(vm.basicInformation.engName);
        // console.log(engName);
        let re = /^[A-Za-z]{1,30}$/;
        if (vm.basicInformation.engName.length === 0) {
          Toast({
            message: '请输入拼音/英文姓名',
            duration: 1000
          });
          return false
        } else if (!re.test(engName)) {
          Toast({
            message: '拼音/英文姓名请输入拼音',
            duration: 1000
          });
          return false
        } else {
          return true
        }
      },

      _handleVisibleChange: function (isVisible) {
        let vm = this;
        if (!vm.isOpen && !isVisible) {
          vm._scrollMove();
        }
        if (vm.isOpen) {
          vm._scrollStop();
          vm.isOpen = false;
        }
      },

      _sendBasicInfo: function () {
        let vm = this;
        let isFirstApply = false;
        if (vm.$tools._isNullOrEmpty(vm.basicInformation.chnName)) {
          _ui.showToast("请输入中文姓名");
          return;
        }
        if (!this._testEngName()) {
          return
        }
        if (!this._testIdno(vm.basicInformation.idNo)) {
          return
        }
        if (vm.$tools._isNullOrEmpty(vm.basicInformation.startDate)) {
          _ui.showToast("请输入证件有效期起始日");
          return;
        }
        if (vm.$tools._isNullOrEmpty(vm.basicInformation.endDate)) {
          _ui.showToast("请输入证件有效期起始日");
          return;
        }
        if (this.basicInformation.endDate !== null && this.basicInformation.endDate !== "") {
          let currentTime = Date.parse(vm.currentDate);
          vm.modifiedDate = this.basicInformation.endDate.substring(0, 4) + "-" +
            this.basicInformation.endDate.substring(4, 6) + "-" +
            this.basicInformation.endDate.substring(6, 8);
          let endValidTimes = Date.parse(vm.modifiedDate);
          let daycount = (endValidTimes - currentTime) / 1000 / 60 / 60 / 24;
          vm.dayCount = daycount;
        }
        if (vm.dayCount < '0') {
          _ui.showToast("您的身份证已过期");
          return;
        } else if (vm.dayCount < '30') {
          _ui.showToast("您填写的身份证有效期在一个月以内,无法办理业务");
          return;
        }

        if (vm.hadinfor) {
          _ui.showToast("已提交的基本信息不可修改");
          return;
        }
        if (!this.checked) {
          MessageBox('', '请选择同意条款！');
          return
        }
        let tests = {
          orderId: this.$store.state.allUserData.orderId,
          idValue: vm.basicInformation.idNo,
          idType: '0',
          name: vm.basicInformation.chnName,
          branchNo: this.$store.state.allUserData.citycode,
          phoneNo: this.$store.state.allUserData.tel
        };
        console.log(tests);
        _ui.showLoading("");
        vm.$Rest._post(vm.$Rest.api._extra, vm.basicInformation)
          .then(function (resp) {
            _ui.hideLoading();
            vm.$Rest._post(vm.$Rest.api._preApprove, tests)
              .then(function (resp) {
                _ui.hideLoading();
                isFirstApply = true;
                vm.$store.commit('changeisFirstApply', isFirstApply);
              })
              .catch(function (err) {
                _ui.hideLoading();
                _ui.showToast(err);
              });
            vm.$router.replace('check');
          })
          .catch(function (err) {
            _ui.hideLoading();
            _ui.showToast(err);
          });
      },

      _openPicker: function (type) {
        let vm = this;
        if (vm.hadinfor) {
          return;
        }
        this.type = type;
        this.pickerValue = new Date(vm.currentDate);
        vm.isOpen = true;
        this.$refs.picker.open();
      },

      _scrollStop() {
        let mo = function (e) {
          e.preventDefault();
        };
        document.body.style.overflow = 'hidden';
        document.addEventListener("touchmove", mo, false);
      },

      _scrollMove() {
        let mo = function (e) {
          e.preventDefault();
        };
        document.body.style.overflow = '';
        document.addEventListener("touchmove", mo, false);
      },

      _handleconfirm(data) {
        let curr_date = data.getDate();
        let curr_month = data.getMonth() + 1;
        let curr_year = data.getFullYear();
        String(curr_month).length < 2 ? (curr_month = "0" + curr_month) : curr_month;
        String(curr_date).length < 2 ? (curr_date = "0" + curr_date) : curr_date;
        let yyyyMMdd = curr_year + "" + curr_month + "" + curr_date;
        let date = yyyyMMdd;
        if (this.type === 'start') {
          this.basicInformation.startDate = date;
        }
        if (this.type === 'end') {
          this.basicInformation.endDate = date;
        }
      },

      _testIdno: function (code) {
        let city = {
          11: "北京",
          12: "天津",
          13: "河北",
          14: "山西",
          15: "内蒙古",
          21: "辽宁",
          22: "吉林",
          23: "黑龙江 ",
          31: "上海",
          32: "江苏",
          33: "浙江",
          34: "安徽",
          35: "福建",
          36: "江西",
          37: "山东",
          41: "河南",
          42: "湖北 ",
          43: "湖南",
          44: "广东",
          45: "广西",
          46: "海南",
          50: "重庆",
          51: "四川",
          52: "贵州",
          53: "云南",
          54: "西藏 ",
          61: "陕西",
          62: "甘肃",
          63: "青海",
          64: "宁夏",
          65: "新疆",
          71: "台湾",
          81: "香港",
          82: "澳门",
          91: "国外 "
        };
        let tip = "";
        let pass = true;
        if (!code || !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(code)) {
          tip = "请填写正确的身份证号";
          pass = false;
        } else if (!city[code.substr(0, 2)]) {
          tip = "地址编码错误";
          pass = false;
        } else {
          //18位身份证需要验证最后一位校验位
          if (code.length === 18) {
            code = code.split('');
            //∑(ai×Wi)(mod 11)
            //加权因子
            let factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
            //校验位
            let parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2];
            let sum = 0;
            let ai = 0;
            let wi = 0;
            for (let i = 0; i < 17; i++) {
              ai = code[i];
              wi = factor[i];
              sum += ai * wi;
            }
            if (parity[sum % 11] !== code[17]) {
              tip = "校验位错误";
              pass = false;
            }
          }
        }
        if (!pass) {
          Toast({
            message: tip,
            duration: 1000
          })
        }
        return pass;
      },
      _takePhoto: function () {
        let vm = this;
        vm.$devices._takePhoto(function (photo) {
          saver.saveAs(photo, "pretty image.jpg");
        }, 0.2);

      }
    }
  }
</script>

