  }

  .next-btn_back {
    text-align: center;
    margin-top: .5rem;
    padding-bottom: .2rem;

    button {
      width: 5rem;
      height: .8rem;
      border: 2px solid #fff;
      color: #fff;
      background: #ddd;
      margin: 0 auto;
      border-radius: 40px;
      outline: none;
    }
  }

</style>



