<template>
  <div id="app">
    <div ref="tabtopwrapper" id="tab-top-wrapper" v-if="showInformation === 'content'" class="content-wrapper">
      <div class="header">
        <mt-navbar
          v-model="selected"
          style="background-color: #fff0;padding-top: 15px">
          <mt-tab-item id="1">
            <img slot="icon" src="./assets/nom_apply.png"/>
            基本信息
          </mt-tab-item>
          <mt-tab-item id="2">
            <img slot="icon" src="./assets/nom_check.png"/>
            卡产品选择
          </mt-tab-item>

          <mt-tab-item id="3">
            <img slot="icon" src="./assets/person.png"/>
            个人信息
          </mt-tab-item>
          <mt-tab-item id="4">
            <img slot="icon" src="./assets/mail.png"/>
            邮寄地址
          </mt-tab-item>

          <mt-tab-item id="5">
            <img slot="icon" src="./assets/verify.png"/>
            增值服务
          </mt-tab-item>
          <mt-tab-item id="6">
            <img slot="icon" src="./assets/picture.png"/>
            附件拍摄
          </mt-tab-item>
        </mt-navbar>
      </div>
      <router-view/>
    </div>
    <auth v-else-if="showInformation === 'login'"></auth>
    <complete v-else-if="showInformation === 'complete'"></complete>
    <verify v-else-if="showInformation === 'verify'"></verify>
    <add v-else-if="showInformation === 'add'"></add>
    <completeBuJian v-else-if="showInformation === 'completeBuJian'"></completeBuJian>
    <error v-else-if="showInformation === 'error'"></error>
  </div>
</template>

<script>
  import './css/commont/reset.css';
  import './css/commont/normalize.css';
  import auth from './components/Auth';
  import complete from './components/complete/Complete';
  import completeBuJian from './components/complete/completeBuJian';
  import verify from './components/verify/VerifyView.vue';
  import add from './components/add/add.vue';
  import error from './components/error';

  export default {
    name: 'App',
    data() {
      return {
        selected: "1",
        isFirstApply: this.$store.state.isFirstApply,
      }
    },
    components: {
      auth,
      complete,
      verify,
      add,
      completeBuJian,
      error
    },
    computed: {
      showInformation() {
        return this.$store.state.showInformation;
      }
    },
    created() {
      this.getUser();
    },
    methods: {
      getUser() {
        let url = window.location.href;
        let allitem = url.split('?')[1].split('#')[0].split('&');
        let itemName;
        let itemValue;
        let allUserData = {};
        allitem.map((item, index) => {
          item.split('=');
          itemName = item.split('=')[0];
          itemValue = item.split('=')[1];
          allUserData[itemName] = decodeURIComponent(itemValue);
        });

        this.$store.commit('ALL_USER_DATA', allUserData)
      }
    },
    watch: {
      //监听页面变化,路由页面
      selected: function (newValue, oldValue) {
        let vm = this;
        switch (newValue) {
          case '1':
            console.log(vm.isFirstApply);
            if (vm.$store.state.isFirstApply) {
              // MessageBox('注意', '请选择同意条款！');
            } else {
              vm.$router.push('basic');
            }
            break;
          case '2':
            vm.$router.replace('check');
            break;
          case '3':
            vm.$router.replace('detail');
            break;
          case '4':
            vm.$router.replace('mail');
            break;
          case '5':
            vm.$router.replace('extra');
            break;
          case '6':
            vm.$router.replace('picture');
            break;
        }
      }
    }
  }
</script>

<style>
  .content-wrapper {
    min-height: 100%;
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    height: 100%;
  }

  .header {
    background: -webkit-gradient(linear, left top, right top, from(#6d99ff), to(#3c6cde));
    /* height: 210px; */
  }

  .mint-navbar .mint-tab-item.is-selected {
    border-bottom: 0 solid #26a2ff;
    color: #26a2ff;
    margin-bottom: -3px;
  }

  .mint-navbar .mint-tab-item {
    z-index: 999;
  }

  .mint-navbar .mint-tab-item img {
    margin-top: 10px;
    height: 35px;
    width: 35px;
    margin-left: -20%;
  }

  .mint-navbar .mint-tab-item.is-selected img {
    width: 45px;
    height: 45px;
    margin-top: 0;
    margin-left: -30%;

  }

  #tab-top-wrapper .mint-navbar .mint-tab-item.is-selected .mint-tab-item-label {
    margin-top: 30px;
    color: #fff;
  }

</style>



