          _ui.showToast("请选择扣款方式");
          return;
        }
        if (vm.$tools._isNullOrEmpty(vm.extraInfo.payAmt)) {
          _ui.showToast("请选择还款金额");
          return;
        }
      }
      _ui.showLoading("");
      vm.$Rest._post(this.$Rest.api._extra, vm.extraInfo)
        .then(function (resp) {
          _ui.hideLoading();
          vm.$router.replace('picture');
        })
        .catch(function (err) {
          _ui.hideLoading();
          _ui.showToast(err);
        });
      },
    }
  }
</script>

